import { inject, injectable } from "../../domain/core/diContainer";
import { ILogger } from "../../domain/infrastructure/ILogger";
import { IGetModelResponse, IQCloudApi } from "../../domain/infrastructure/IQCloudApi";
import { IApplication, IFolder, IModel, IModule, ITreeviewItem } from "../../domain/model/models";
import { INavItem } from "../../domain/model/ui/INavItem";
import { IEditorManager } from "../../domain/objects/IEditorManager";
import { INotification } from "../../domain/presentation/INotification";
import { IViewModelProxifier } from "../../domain/presentation/IViewModelProxifier";
import { IFileExplorer } from "../../domain/useCase/IFileExplorer";
import { IStudio } from "../../domain/useCase/IStudio";
import { IViewModel } from "../../domain/viewModel/IViewModel";
import { IViewModelManager } from "../../domain/viewModel/IViewModelManager";

@injectable()
export class Studio implements IStudio {
    constructor(
        @inject(IViewModel) private readonly viewModel: IViewModel,
        @inject(IViewModelManager) private readonly viewModelManager: IViewModelManager,
        @inject(IFileExplorer) private readonly fileExplorer: IFileExplorer,
        @inject(IQCloudApi) private readonly qcloudApi: IQCloudApi,
        @inject(IEditorManager) private readonly editorManager: IEditorManager,
        @inject(ILogger) private readonly logger: ILogger,
        @inject(INotification) private readonly notification: INotification,
        @inject(IViewModelProxifier) private readonly proxifier: IViewModelProxifier
    ) { }

    public async openItem(item: IModel, options: {fromHistory?: boolean, noFocus?: boolean} = {}) {
        const studio = this.viewModel.studio;

        if (!options.fromHistory && studio?.currentItem?.ID == item.ID) {
            return;
        }

        if (studio.currentItem?.modified) {
            const targetEditor = this.editorManager.getEditor(studio.currentItem)!;
            const model = await targetEditor.getModel();
            studio.currentItem.modelBody = this.proxifier.markRaw(model.model);
            studio.currentItem.state = this.proxifier.markRaw(model.state);
        }

        let itemOpen = false;
        if (!studio.openItems) {
            studio.openItems = [];
        } else {
            itemOpen = !!studio.openItems.find((openItem) => openItem.ID == item.ID);
        }

        if (!item.modelBody || (!itemOpen && !item.checkedOut)) {
            let model: IGetModelResponse;
            if(options.fromHistory){
                model = await this.qcloudApi.getModelInfo(item.ID);
                //item.modelBody already comes by history selection.
            }
            else{
                model = await this.qcloudApi.getModel(item.ID);
                item.modelBody = this.proxifier.markRaw(model.model);
            }
            item.checkedOut = model.checkouts?.some(
                (owner) => owner.user == this.viewModel.user?.email
            );
        }

        if (!studio.tabOrder) {
            studio.tabOrder = [];
        }
        if (itemOpen) {
            const orderIndex = studio.tabOrder.findIndex((itemID) => item.ID == itemID);
            studio.tabOrder.unshift(studio.tabOrder.splice(orderIndex, 1)[0]);
        } else {
            item.modified = false;
            studio.openItems.push(item);
            if (options.noFocus){
                studio.tabOrder.splice(1,0,item.ID);
            }else{
                studio.tabOrder.unshift(item.ID);
            }
        }

        if (options.noFocus){ return; }

        studio.currentItem = item;
        this.fileExplorer.selectItem(item.ID);
        const targetEditor = this.editorManager.getEditor(item)!;
        await targetEditor.setModel(item);
        this.editorManager.show(targetEditor, true);
    }
    public async openHistoryItem(historyID: string) {
        const historyItem = await this.qcloudApi.getModelHistory(historyID);
        const modelItem: IModel = {
            ID: historyItem.modelID,
            name: historyItem.name,
            modelBody: historyItem.model,
            modelType: historyItem.modelType,
            objectType: "model",
            parent: this.viewModelManager.findObject(historyItem.ownerItem) as IFolder | IModule | IApplication,
            synced: true,
            additionals: historyItem.modelAdditionals,
        };
        await this.openItem(modelItem, {fromHistory: true});
    }
    public async closeItem(item: IModel, options: { silent?: boolean } = {}) {
        if (item.modified && !options.silent) {
            const result = await this.notification.showConfirm({
                type: "warning",
                title: `Do you want to save changes made on ${item.name} ?`,
                text: "All unsaved changes will be lost",
                approveText: "Save",
                cancelText: "Cancel",
                rejectText: "Discard",
            });
            if (result.result == "cancel") {
                return;
            }
            if (result.result == "approve") {
                try {
                    item.synced = false;
                    await this.save(item);
                } finally {
                    item.synced = true;
                }
            }
        }
        item.modelBody = undefined;
        item.state = undefined;

        const studio = this.viewModel.studio;
        studio.currentItem = undefined;
        if (studio.openItems) {
            const removeIndex = studio.openItems.findIndex((openItem) => openItem.ID == item.ID);
            studio.openItems.splice(removeIndex, 1);
        }
        
        let nextItemID: string | undefined;
        if (studio.tabOrder) {
            const removeTabOrder = studio.tabOrder.findIndex((itemID) => itemID == item.ID);
            studio.tabOrder!.splice(removeTabOrder, 1);
            nextItemID = studio.tabOrder[0];
        }
        if (nextItemID) {
            const newItem = studio.openItems!.find((openItem) => openItem.ID == nextItemID)!;
            this.openItem(newItem);
        } else {
            this.fileExplorer.unselectItem(item.ID);
            const targetEditor = this.editorManager.getEditor(item)!;
            targetEditor.show(false);
        }
    }
    public attach(element: HTMLDivElement): void {
        this.editorManager.attach(element);
    }
    public detach() {
        this.editorManager.detach();
    }
    public async openApp(appId: string) {
        const backendItemsProm = this.qcloudApi.listTreeviewItems(appId);
        const currentAppProm = this.qcloudApi.getApplication(appId);
        const [backendItems, currentApp] = await Promise.all([backendItemsProm, currentAppProm]);

        const appItems = this.convertBackendItems(backendItems, currentApp);
        this.viewModel.studio = {
            appId,
            ui: this.defaultUI(),
            items: appItems,
            appName: currentApp.name,
        };
        this.logger.log({ level: "log", message: `App ${appId} opened` });
    }

    public async checkout(item: IModel) {
        if (item.modified) {
            const result = await this.notification.showConfirm({
                text: "Your changes on ReadOnly Mode will be discarded",
                approveText: "Discard",
                cancelText: "Cancel",
                type: "warning",
            });
            if (!result.approved) {
                return;
            }
        }

        const info = await this.qcloudApi.getModel(item.ID, {
            createCheckout: true,
            checkoutControl: true,
        });
        item.modelBody = info.model;
        item.modified = false;
        item.checkedOut = true;

        const targetEditor = this.editorManager.getEditor(item)!;
        await targetEditor.setModel(item);
    }

    public async save(item: IModel) {
        const targetEditor = this.editorManager.getEditor(item)!;
        const model = await targetEditor.getModel();

        item.modelBody = this.proxifier.markRaw(model.model);
        item.state = this.proxifier.markRaw(model.state);
        await this.qcloudApi.updateModel(item.ID, { model: item.modelBody });
        item.modified = false;

        this.notification.showNotification({ text: `${item.name} saved successfully`, type: "success" });
    }
    public async checkin(
        item: IModel,
        options: {
            shortComment: string;
            longComment?: string;
            version: "major" | "minor";
        }
    ) {
        const targetEditor = this.editorManager.getEditor(item)!;
        const model = await targetEditor.getModel();
        item.modelBody = this.proxifier.markRaw(model.model);
        item.state = this.proxifier.markRaw(model.state);

        const checkin = options.version == "minor";
        const publish = options.version == "major";
        const shortComment = options.shortComment;
        const comment = options.longComment;
        await this.qcloudApi.updateModel(item.ID, {
            checkin,
            publish,
            shortComment,
            comment,
            model: item.modelBody,
        });
        item.modified = false;
        item.checkedOut = false;
    }
    public async release(item: IModel) {
        if (item.modified) {
            const result = await this.notification.showConfirm({
                text: "Your changes will be discarded",
                approveText: "Discard",
                cancelText: "Cancel",
                type: "warning",
            });
            if (!result.approved) {
                return;
            }
        }
        const revertedItemProm = this.qcloudApi.getModelBody(item.ID);
        await this.qcloudApi.updateModel(item.ID, { release: true });
        item.modelBody = (await revertedItemProm).model;
        item.modified = false;
        item.checkedOut = false;
        if (this.viewModel.studio.currentItem?.ID != item.ID) {
            return;
        }

        const targetEditor = this.editorManager.getEditor(item)!;
        await targetEditor.setModel(item);
    }

    public nav_addNavItems(addItems: Array<INavItem>): void {
        this.viewModel.studio.ui = this.defaultUI();
        const navItems = (this.viewModel.studio.ui.navItems =
            this.viewModel.studio.ui.navItems || []);
        addItems.forEach((item) => navItems.push(item));
    }
    public async nav_unselect() {
        this.viewModel.studio.ui!.navItems?.forEach((item) => (item.selected = false));
    }
    public async nav_selectItem(navItem: INavItem) {
        this.viewModel.studio.ui!.navItems?.forEach((item) => (item.selected = false));
        navItem.selected = true;
        await navItem.onSelect?.();
    }
    public nav_removeNavItems(removeItems: Array<INavItem>) {
        let navItems = this.viewModel.studio.ui!.navItems;
        if (!navItems) {
            return;
        }

        //these are menu items, max 6-7. No need for indexes, just search.
        this.viewModel.studio.ui!.navItems = navItems.filter(
            (navItem) => !removeItems.find((removeItem) => navItem.name == removeItem.name)
        );
    }

    private defaultUI() {
        return this.viewModel.studio.ui || { sidebar: { left: 293 } };
    }


    public convertBackendItems(backendItems: Array<ITreeviewItem>, app: IApplication): Array<IModule | IFolder | IModel> {
        const converter = (items: Array<ITreeviewItem>, itemParent: IApplication | IModule | IFolder) =>
            items.map((item) => {
                const parent = itemParent as IApplication;
                if (item.type == "module") {
                    const module: IModule = {
                        ID: item.id,
                        name: item.name,
                        objectType: "module",
                        synced: true,
                        parent,
                        children: [],
                    };
                    this.viewModelManager.moduleCreated(module);
                    module.children = item.children ? (converter(item.children, module) as Array<IModel | IFolder>) : [];
                    return module;
                } else if (item.type == "folder") {
                    const parent = itemParent as IApplication | IModule | IFolder;
                    const folder: IFolder = {
                        ID: item.id,
                        name: item.name,
                        objectType: "folder",
                        synced: true,
                        parent,
                        children: [],
                    };
                    this.viewModelManager.folderCreated(folder);
                    folder.children = item.children ? (converter(item.children, folder) as Array<IModel | IFolder>) : [];
                    return folder;
                } else {
                    const parent = itemParent as IApplication | IModule | IFolder;
                    const model: IModel = {
                        ID: item.id,
                        modelType: item.type,
                        usageType: item.usageType,
                        additionals: item.modelAdditionals,
                        name: item.name,
                        objectType: "model",
                        synced: true,
                        parent,
                    };
                    this.viewModelManager.modelCreated(model);
                    return model;
                }
            });
        return converter(backendItems, app);
    }
}
