
export type HookCallBack = (...args: Array<any>) => void;
export type HookErrorHandler = (error: unknown, hookName: string) => void;
export interface IHookOptions {
    name: string;
    manageState?: boolean;
    autoTriggerPostSubscribers?: boolean;
    errorHandler?: HookErrorHandler
};

export interface IHook<T extends HookCallBack = HookCallBack> {
    autoTriggerPostSubscribers: boolean;

    setUp(options: IHookOptions): void;
    ResetAndStartState(): void;
    DisableState(): void
    IsTriggered(): boolean;

    readonly hasSubscribers: boolean;
    readonly subscriberCount: number;
    readonly hasPostSubscribers: boolean;
    readonly triggerer: T;

    triggerPostSubscribers(...args: Array<any>): void
    postSubscribe(cb: T): void;
    postUnsubscribe(cb: T): void;
    clearPostSubscribers(forced?: boolean): void;
    forceClearAllSubscibers(): void;
    subscribe(cb: T): void;
    clearSubscribers(): void;
    unsubscribe(cb: T): void;
}
export const IHook = Symbol.for("IHook");