import "reflect-metadata";
import { container } from "./domain/core/diContainer";
import { appStartUp } from "./application/startUp";
import { IPresentation } from "./domain/presentation/IPresentation";
import { infraStartUp } from "./infrastructure/startUp";
import { presentationStartUp } from "./presentation/startUp";

async function main() {
    //startups
    await Promise.all([appStartUp, infraStartUp, presentationStartUp].map(startup => startup.register(container)));

    //start
    container.resolve<IPresentation>(IPresentation).start();
}

main();