import { injectable, inject } from "../../domain/core/diContainer";
import { IQCloudApi } from "../../domain/infrastructure/IQCloudApi";
import { createError } from "../../domain/model/shellError";
import { IListExportJobArtifacts, IListExportJobArtifactsParam } from "../../domain/useCase/IListExportJobArtifacts";
import { IViewModelManager } from "../../domain/viewModel/IViewModelManager";

@injectable()
export class ListExportJobArtifactsImpl implements IListExportJobArtifacts {
    constructor(
        @inject(IQCloudApi) private readonly qcloudApi: IQCloudApi,
        @inject(IViewModelManager) private readonly viewModelManager: IViewModelManager,
    ) { }

    public async execute(param: IListExportJobArtifactsParam) {
        if (!param.jobID) {
            throw createError({ message: "'jobID' is required", type: "business" });
        }

        const artifacts = await this.qcloudApi.listExportJobArtifacts(param.jobID);
        this.viewModelManager.setExportJobArtifacts(param.jobID, artifacts);
    }
}