import { Watcher, } from "../../common/watcher";
import { inject, injectable, container } from "../../domain/core/diContainer";
import { IApplication, IApplicationDetails, IExportJobArtifactInfoItem, IExportJobItem, IExportJobStepItem, IFolder, IModel, IModule, IUser } from "../../domain/model/models";
import { IObject } from "../../domain/model/IObject";
import { IViewModel } from "../../domain/viewModel/IViewModel";
import { IViewModelManager } from "../../domain/viewModel/IViewModelManager";
import { IState } from "../../domain/model/IState";
import { INavItem } from "../../domain/model/ui/INavItem";
import { IViewModelProxifier } from "../../domain/presentation/IViewModelProxifier";
import { IStudio } from "../../domain/useCase/IStudio";

/**
 * <DOCS>
 * new Objects placed on viewModel are proxified however their handles are not!
 *      //THIS IS OK
 *      this.viewModel.blabla = param
 *      this.viewModel.blabla.xyz // this is ok
 * 
 *      //THIS IS NOT!!
 *      this.viewModel.blabla = param
 *      blabla.xyz // this is not!! -->> blabla = proxify(blabla)  \n  this.viewModel.blabla = param  \n  blabla.xyz
 */

function vmCreator(): IViewModel {
    return { apps: [], exportJobs: [], studio: { appId: "", appName: "", items: [] } }; // VM 
    //TODO: studio'nun nullable olmas lazm.
}
@injectable()
export class ViewModelManager implements IViewModelManager {
    public static reset() {
        const proxifier = container.resolve<IViewModelProxifier>(IViewModelProxifier);

        (container as any)._registry._registryMap.delete(IState);
        if (!container.isRegistered(IViewModel)) {
            const vm = vmCreator();
            container.registerInstance<IViewModel>(IViewModel, proxifier.proxify(vm));
        } else {
            const vm = container.resolve<IViewModel>(IViewModel) as any;
            const newVm = vmCreator() as any;

            const newKeys = Object.keys(newVm).reduce<Set<string>>((prev, key) => { prev.add(key); return prev; }, new Set());
            Object.keys(newVm).forEach(key => vm[key] = newVm[key]);
            Object.keys(vm).filter(key => !newKeys.has(key)).forEach(key => vm[key] = undefined);
        }
        container.registerInstance<IState>(IState, { objectDict: {} });
    }
    constructor(
        @inject(IViewModel) private viewModel: IViewModel,
        @inject(IState) private readonly state: IState,
        @inject(IViewModelProxifier) private readonly proxifier: IViewModelProxifier,
    ) { }


    public reset() {
        ViewModelManager.reset();
    }
    public get userLoggedIn() {
        return this.viewModel.user?.loggedIn || false;
    }
    public set userLoggedIn(value: boolean) {
        this.viewModel.user!.loggedIn = value; // no user should crash
    }
    public initSessionUser(email: IUser["email"], domains: IUser["domains"]): void {
        this.viewModel.user = { email, loggedIn: false, domains };
        if (domains.length == 1) { // #54 => Single tenant auto select
            this.viewModel.user.selectedDomain = domains[0];
        }
    }



    public setApplicationList(apps: Array<IApplication>) {
        this.viewModel.apps = apps;
        this.viewModel.apps.forEach(app => {
            app.synced = true;
            this.state.objectDict[app.ID] = app;
        });
    }
    public setApplicationDetails(appID: string, details: IApplicationDetails) {
        const app = this.state.objectDict[appID]! as IApplication;
        app.details = details;
    }
    public setExportJobItemList(jobs: Array<IExportJobItem>) {
        this.viewModel.exportJobs = jobs;
        this.viewModel.exportJobs.forEach(job => {
            this.state.objectDict[job.jobID] = job;
        });
    }
    public setExportJobSteps(jobID: string, steps: Array<IExportJobStepItem>) {
        const job = this.viewModel.exportJobs!.find(i => i.jobID == jobID)!;
        job.steps = steps;
        job.steps!.forEach(step => {
            this.state.objectDict[step.ID] = step;
        });
    }
    public setExportJobStepLogs(stepID: string, logs: Array<string>) {
        const step = this.state.objectDict[stepID] as IExportJobStepItem;
        step.logs = logs;
    }
    public setExportJobArtifacts(jobID: string, artifacts: Array<IExportJobArtifactInfoItem>) {
        const job = this.viewModel.exportJobs!.find(i => i.jobID == jobID)!;
        job.artifacts = artifacts;
        job.artifacts!.forEach(artifact => {
            this.state.objectDict[artifact.ID] = artifact;
        });
    }


    public modelCreated(model: IModel): void {
        this.watchObjectID(model);
        this.addToParentChildren(model);
        this.state.objectDict[model.ID] = model;
    }
    public moduleCreated(module: IModule): void {
        module = this.proxifier.proxify(module);
        this.watchObjectID(module);
        this.addToParentChildren(module);
        this.state.objectDict[module.ID] = module;
    }
    public folderCreated(folder: IFolder) {
        folder = this.proxifier.proxify(folder);
        this.watchObjectID(folder);
        this.addToParentChildren(folder);
        this.state.objectDict[folder.ID] = folder;
    }
    public applicationCreated(app: IApplication) {
        app = this.proxifier.proxify(app);
        this.watchObjectID(app);
        this.viewModel.apps.push(app);
        this.state.objectDict[app.ID] = app;
        return app;
    }
    public exportJobCreated(job: IExportJobItem) {
        this.viewModel.exportJobs?.unshift(job);
    }
    private watchObjectID(object: IObject) {
        new Watcher(object, "ID").onChange((newId, oldId) => {
            this.state.objectDict[newId] = this.state.objectDict[oldId];
            delete this.state.objectDict[oldId];
        });
    }
    private addToParentChildren(object: IModel | IModule | IFolder) {
        if (!object.parent || object.parent.objectType == "application" || object.objectType == "module"){
            this.viewModel.studio.items.push(object);
            return;
        }

        object.parent.children = object.parent.children || [];
        if (!object.parent.children.find(item => item == object)) {
            object.parent.children.push(object);
        }
    }
    private removeFromParentChildren(object: IModel | IModule | IFolder) {
        if (!object.parent || object.parent.objectType == "application") {
            const objectIndex = this.viewModel.studio.items.findIndex(item => item.ID == object.ID);
            this.viewModel.studio.items.splice(objectIndex, 1);
            return;
        }

        object.parent.children = object.parent.children?.filter(child => child.ID != object.ID);
    }



    public findApplication(name: string) {
        return this.viewModel.apps.find(app => app.name == name);
    }
    public findObject(objectId: string) {
        return this.state.objectDict[objectId];
    }



    public objectUpdated<TObject extends IApplication | IModule | IModel>(object: TObject): TObject {
        const curObj = this.state.objectDict[object.ID];
        const newObj = this.proxifier.proxify({ ...curObj, ...object });
        this.state.objectDict[object.ID] = newObj;
        if (newObj.objectType == "application") {
            const appIndex = this.viewModel.apps.findIndex(app => app.ID == newObj.ID);
            this.viewModel.apps[appIndex] = newObj
        }
        return newObj;
    }



    public removeExportJob(jobID: string) {
        this.viewModel.exportJobs = this.viewModel.exportJobs?.filter(item => item.jobID != jobID);
    }
    public removeModel(model: IModel): void {
        this.removeFromParentChildren(model);
        delete this.state.objectDict[model.ID];
    }
    public removeFolder(folder: IFolder): void {
        this.removeFromParentChildren(folder);
        delete this.state.objectDict[folder.ID];
    }
    public removeModule(module: IModule): void {
        this.removeFromParentChildren(module);
        delete this.state.objectDict[module.ID];
    }
    public removeApplication(app: IApplication): void {
        this.viewModel.apps = this.viewModel.apps.filter(item => item.ID != app.ID);
        delete this.state.objectDict[app.ID];
    }
    public removeObject(object: IObject) {
        switch (object.objectType) {
            case "model": this.removeModel(object as IModel); break;
            case "folder": this.removeFolder(object as IFolder); break;
            case "module": this.removeModule(object as IModule); break;
            case "application": this.removeApplication(object as IApplication); break;
        }
    }
}