import { inject, injectable } from "../domain/core/diContainer";
import { ILogger } from "../domain/infrastructure/ILogger";
import { ITimer, ITimerOptions } from "../domain/infrastructure/ITimer";

@injectable()
export class TimerImpl implements ITimer {
    private options?: ITimerOptions;
    private mainInterval?: number;
    private targetTime?: any;
    private lastIntervalCall: number = Date.now();

    constructor(
        @inject(ILogger) private readonly logger: ILogger,
    ) { }

    public reset() {
        if (!this.mainInterval) { return; }

        window.clearInterval(this.mainInterval);
    }

    public setUp(options: ITimerOptions) {
        this.options = options;
        const now = Date.now();
        this.targetTime = now + this.options.time;
        this.lastIntervalCall = now;

        this.mainInterval = window.setInterval(() => {
            const now = Date.now();
            if (options.interval && now - this.lastIntervalCall >= options.interval.time) {
                try {
                    options.interval.callback();
                } catch (error) { this.logger.log({ level: "error", error, message: "timer interval error" }); }
                this.lastIntervalCall = now;
            }
            if (now >= this.targetTime) {
                this.reset();
                options.callback();
            }
        }, 1000);
    }

    public restart() {
        this.reset();
        if (!this.options) { return; }

        this.setUp(this.options);
    }
}