import { inject, injectable } from "../domain/core/diContainer";
import { HookCallBack, HookErrorHandler, IHook } from "../domain/infrastructure/IHook";
import { ILogger } from "../domain/infrastructure/ILogger";

@injectable()
export class HookImpl<T extends HookCallBack = HookCallBack> implements IHook {
    private subscribers: Array<T> = [];
    private postSubscribers?: HookImpl<T>;
    private hookName: string = "";
    private errorHandler?: HookErrorHandler;
    private hookState: { triggered: boolean, triggerArgs: Array<any>, active: boolean } = { triggered: false, triggerArgs: [], active: false }; //having a hookState states this hook manages state
    private _autoTriggerPostSubscribers: boolean = true;

    public get autoTriggerPostSubscribers(): boolean {
        return this._autoTriggerPostSubscribers;
    }
    public set autoTriggerPostSubscribers(v: boolean) {
        this._autoTriggerPostSubscribers = v;
    }

    public constructor(
        @inject(ILogger) private readonly logger: ILogger,
    ) { }
    public setUp(options: { name: string, manageState?: boolean, autoTriggerPostSubscribers?: boolean, errorHandler?: HookErrorHandler }) {
        this.hookName = options.name;
        this.errorHandler = options.errorHandler;
        if (options.autoTriggerPostSubscribers === false) {
            this._autoTriggerPostSubscribers = false;
        }
        if (options.manageState) {
            this.hookState.active = options.manageState;
        }
    }

    public ResetAndStartState() {
        this.hookState.triggerArgs = [];
        this.hookState.active = true;
        this.hookState.triggered = false;
    }

    public DisableState() {
        this.hookState.triggerArgs = [];
        this.hookState.active = false;
        this.hookState.triggered = false;
    }

    public IsTriggered() {
        if (!this.hookState.active) { throw new Error("IsTriggered can not be checked on a non manageState Hook: " + this.hookName); }

        return this.hookState.triggered;
    }

    private getHookName() {
        let retVal = this.hookName;

        if (!retVal.toLowerCase().endsWith("hook")) {
            retVal += " hook";
        }
        return retVal;
    }

    public get hasSubscribers() {
        return this.subscriberCount > 0;
    }

    public get subscriberCount() {
        return this.subscribers.length;
    }

    public get hasPostSubscribers(): boolean {
        return (this.postSubscribers?.hasPostSubscribers || this.postSubscribers?.hasSubscribers) === true;
    }

    public get triggerer(): T {
        return this.trigger as T;
    }

    private trigger(...args: Array<any>) {
        if (this.hookState.active) {
            this.hookState.triggered = true;
            this.hookState.triggerArgs = args;
        }
        this.subscribers.forEach(sub => this.tryTriggerSubscriber(sub, args));
        if (!this._autoTriggerPostSubscribers) this.postSubscribers?.triggerer.apply(this.postSubscribers, args)
    }

    private tryTriggerSubscriber(sub: T, args: Array<any>) {
        try {
            sub.apply(null, args);
        } catch (error) {
            this.logger.log({ message: `Hook: ${this.getHookName()} subcriber failed`, error: error, level: "error" });
            this.errorHandler?.(error, this.getHookName());
        }
    }

    public triggerPostSubscribers(...args: Array<any>) {
        this.postSubscribers?.triggerer.apply(this.postSubscribers, args);
    }

    public postSubscribe(cb: T) {
        if (!this.postSubscribers) {
            this.postSubscribers = new HookImpl<T>(this.logger);
            this.postSubscribers.setUp({ name: this.hookName + "_Post", manageState: this.hookState.active });
            this.postSubscribers.hookState = this.hookState;
        }
        this.postSubscribers.subscribe(cb);
    }
    public postUnsubscribe(cb: T) {
        this.postSubscribers?.unsubscribe(cb);
    }
    public clearPostSubscribers(forced: boolean = false) {
        this.postSubscribers?.clearSubscribers();
        if (forced || !this.hasPostSubscribers) {
            this.postSubscribers = undefined;
        }
    }

    public forceClearAllSubscibers() {
        this.clearPostSubscribers(true);
        this.clearSubscribers();
    }

    public subscribe(cb: T) {
        this.subscribers.push(cb);
        this.hookState.active && this.hookState.triggered && this.tryTriggerSubscriber(cb, this.hookState.triggerArgs);
    }

    public clearSubscribers() {
        this.subscribers = [];
    }

    public unsubscribe(cb: T) {
        this.subscribers = this.subscribers.filter(sub => sub != cb); // splice is fastest but this is safest, because of immutability, because of forEach traversals
    }
}