import { inject, injectable } from "../../domain/core/diContainer";
import { IQCloudApi } from "../../domain/infrastructure/IQCloudApi";
import { IGetUserDomains, IGetUserDomainsParam } from "../../domain/useCase/IGetUserDomains";
import { IViewModelManager } from "../../domain/viewModel/IViewModelManager";

@injectable()
export class GetUserDomainsImpl implements IGetUserDomains {
    constructor(
        @inject(IViewModelManager) private readonly viewModelManager: IViewModelManager,
        @inject(IQCloudApi) private readonly qcloudApi: IQCloudApi,
    ) { }

    public async execute(param: IGetUserDomainsParam) {
        const domains = await this.qcloudApi.getUserDomains(param.email);
        this.viewModelManager.initSessionUser(param.email, domains);
        return domains;
    }

    public async fail(err: unknown) {  
    }
}