import { injectable, inject } from "../../domain/core/diContainer";
import { IQCloudApi } from "../../domain/infrastructure/IQCloudApi";
import { IListExportJobs, IListExportJobsParam } from "../../domain/useCase/IListExportJobs";
import { IViewModelManager } from "../../domain/viewModel/IViewModelManager";

@injectable()
export class ListExportJobsImpl implements IListExportJobs {
    constructor(
        @inject(IQCloudApi) private readonly qcloudApi: IQCloudApi,
        @inject(IViewModelManager) private readonly viewModelManager: IViewModelManager,
    ) { }

    public async execute(param: IListExportJobsParam) {
        const jobs = await this.qcloudApi.listExportJobs();

        jobs.forEach(job => {
            const createDate = new Date(job.createDate);
            job.createDate = createDate.toLocaleString();
        });

        this.viewModelManager.setExportJobItemList(jobs);
    }
}