export interface IAsyncComponentCreator {
    instantiateComponent(asyncComponent: object, props: Record<string, unknown> | undefined, mountPoint: HTMLElement | undefined): object;
    destroyComponent(componentInstance: object): void;


    /**
     * @param componentPath sidebar component path under /components/sidebars/...
     */
    createSidebarComponent(componentPath: string): object;
    createLoginDialogComponent(): object;
    createNewItemComponent(): object;
    createModelHistoriesComponent(): object;
    createEditModuleComponent(): object;
    createProcessEditor(): object;
    createProcessDesignEditor(): object;
}

export const IAsyncComponentCreator = Symbol.for("IAsyncComponentCreator");