import { injectable } from "../domain/core/diContainer";
import { ILocalization } from "../domain/infrastructure/ILocalization";
import * as en from "../settings/localization/en";
import * as tr from "../settings/localization/tr";

const langs = { en: en.langData, tr: tr.langData };

@injectable()
export class LocalizationImpl implements ILocalization {
    private readonly curLang = "en";

    public get(lrid: string): string {
        return langs[this.curLang][lrid];
    }
}