import { App, createApp } from "vue";
import { container, injectable } from "../../../domain/core/diContainer";
import { IInlineEditor } from "../../../domain/infrastructure/IInlineEditor";
import { IModelBodyObject, IModel } from "../../../domain/model/models";
import { IProcessDesignEditor } from "../../../domain/objects/editors/IProcessDesignEditor";
import { IAsyncComponentCreator } from "../../../domain/presentation/IAsyncComponentCreator";
import { IViewModel } from "../../../domain/viewModel/IViewModel";

export interface IProcessDesignModel {
    bpmn: any;
}
export interface IProcessDesignEditorProps extends Record<string, unknown> {
    name: string;
    functions: {
        setModel?: (model: IProcessDesignModel, state: any) => Promise<void>;
        getModel?: () => Promise<{ model: IProcessDesignModel, state: any }>;
    },
    callbacks: {
        modified: () => void;
    }
}
interface IProcessEditorData {
    editor: object;
    props: IProcessDesignEditorProps;
}

@injectable()
export class ProcessDesignEditorImpl implements IProcessDesignEditor {
    private editorData?: IProcessEditorData;
    constructor(
        private readonly compCreator = container.resolve<IAsyncComponentCreator>(IAsyncComponentCreator),
        private readonly inlineEditor = container.resolve<IInlineEditor>(IInlineEditor),
        private readonly viewModel = container.resolve<IViewModel>(IViewModel),
    ) { }
    public name = "processDesign" as "processDesign";
    public connect() { }
    public attach(element: HTMLElement): void {
        this.inlineEditor.attach(this.name, element);

        const props: IProcessDesignEditorProps = { name: "", functions: {}, callbacks: { modified: () => this.modified() } };
        const editor = this.compCreator.instantiateComponent(this.compCreator.createProcessDesignEditor(), props, this.inlineEditor.Root);
        this.editorData = { editor, props };
    }
    public detach(): void {
        if (!this.editorData) { return; }

        this.compCreator.destroyComponent(this.editorData.editor);
        delete this.editorData;
    }
    public show(show: boolean): void {
        this.inlineEditor.show(show);
    }
    public async getModel(): Promise<{ model: IModelBodyObject[]; state: any; }> {
        const model = await this.editorData!.props.functions.getModel!();
        return {
            model: [
                { key: "bpmn", model: model.model.bpmn }
            ],
            state: model.state
        };
    }
    public async setModel(item: IModel) {
        const state = undefined as any;
        const model = item.modelBody!.reduce<IProcessDesignModel>((model, curModel) => {
            if (curModel.key == "bpmn") {
                model.bpmn = curModel.model;
            }
            return model;
        }, { bpmn: "" });
        await this.editorData!.props.functions.setModel!(model, state);
    }

    private modified() {
        const currentItem = this.viewModel.studio.currentItem;
        if (!currentItem) { return; }

        currentItem.modified = true;
    }

}