import { injectable, inject } from "../../domain/core/diContainer";
import { IQCloudApi } from "../../domain/infrastructure/IQCloudApi";
import { IModelVersionItem } from "../../domain/model/models";
import { createError } from "../../domain/model/shellError";
import { IListModelVersions, IListModelVersionsParam } from "../../domain/useCase/IListModelVersions";

@injectable()
export class ListModelVersionsImpl implements IListModelVersions {
    constructor(
        @inject(IQCloudApi) private readonly qcloudApi: IQCloudApi
    ) { }
    public async execute(param: IListModelVersionsParam) {
        if (!(param.modelID && param.versionType)) {
            throw createError({ message: "'modelID' and '' are required!", type: "business" });
        }

        const histories = await this.qcloudApi.listModelHistories(param.modelID, param.versionType == "major" ? "publish" : "checkin");
        return histories;
    }
}