import { delayTillResolve } from "../domain/core/diContainer";
import { IStartUp } from "../domain/core/IStartUp";
import { IConfig } from "../domain/infrastructure/IConfig";
import { ILogger } from "../domain/infrastructure/ILogger";
import { IPresentation } from "../domain/presentation/IPresentation";
import { ITreeView } from "../domain/presentation/ITreeView";
import { IZindex } from "../domain/presentation/IZindex";
import { TreeView } from "./all/impls/treeview";
import { ZIndexImpl } from "./all/impls/zindexImpl";


export const presentationStartUp: IStartUp = {
    async register(container) {
        container.register<ITreeView>(ITreeView, TreeView);
        container.registerInstance<IZindex>(IZindex, new ZIndexImpl());
        await delayTillResolve([IConfig, ILogger]);
        const config = container.resolve<IConfig>(IConfig);
        const type = config.getPresentationLayer();
        switch (type) {
            case "html":
                container.register<IPresentation>(IPresentation, (await import("./html/index")).HtmlPresentation);
                break;
            case "vue3":
                container.register<IPresentation>(IPresentation, (await import("./vue3")).Vue3Presentation);
                break;
        }
    }
}