import { defineAsyncComponent } from "vue";
import { injectable, inject } from "../../domain/core/diContainer";
import { IAsyncComponentCreator } from "../../domain/presentation/IAsyncComponentCreator";
import { IDialog } from "../../domain/presentation/IDialog";
import { IRouter } from "../../domain/presentation/IRouter";
import { ILogout } from "../../domain/useCase/ILogout";
import { ITimedOut, ITimedOutParam } from "../../domain/useCase/ITimedOut";
import { IUseCaseExecutor } from "../../domain/useCase/IUseCaseExecutor";
import { IViewModel } from "../../domain/viewModel/IViewModel";

@injectable()
export class TimedOutImpl implements ITimedOut {
    constructor(
        @inject(IRouter) private readonly router: IRouter,
        @inject(IDialog) private readonly dialog: IDialog,
        @inject(IViewModel) private readonly viewModel: IViewModel,
        @inject(IUseCaseExecutor) private readonly executor: IUseCaseExecutor,
        @inject(IAsyncComponentCreator) private readonly compCreator: IAsyncComponentCreator,
    ) { }
    public async execute() {
        if (!this.viewModel.user?.loggedIn) {
            this.executor.execute(async () => {
                this.executor.executeUseCase<ILogout>(ILogout);
            }, { loading: true });
            return;
        }
        this.dialog.showDialog(this.compCreator.createLoginDialogComponent(), {closable: false,title: "Timed out. Please login"});
        //TODO: adamn u anki sayfasna bak proje sayfasndaysa login popup kar, sfr giren adam aa devam
    }
}