import { container, inject, injectable } from "../../domain/core/diContainer";
import { ISidebarManager } from "../../domain/objects/ISidebarManager";
import { IAsyncComponentCreator } from "../../domain/presentation/IAsyncComponentCreator";
import { IViewModel } from "../../domain/viewModel/IViewModel";

@injectable()
export class SidebarManager implements ISidebarManager {
    private components: Record<string, object | undefined> = {};
    constructor(
        @inject(IViewModel) private readonly viewModel: IViewModel = container.resolve<IViewModel>(IViewModel),
        @inject(IAsyncComponentCreator) private readonly asyncCompCreator: IAsyncComponentCreator = container.resolve<IAsyncComponentCreator>(IAsyncComponentCreator),
    ) { }

    private getui() {
        return this.viewModel.studio.ui!;
    }
    public show(sidebarComponent: string) {
        let sidebar = this.getui().sidebar;
        sidebar.componentPath = sidebarComponent;
        let sidebarComp = this.components[sidebarComponent];
        if (!sidebarComp) {
            sidebarComp = this.components[sidebarComponent] = this.asyncCompCreator.createSidebarComponent(sidebarComponent);
        }
        sidebar.component = sidebarComp;
    }
    public hide() {
        const ui = this.getui();
        ui.sidebar = { left: ui.sidebar.left };
    }
    public reset(): void {
        this.components = {};
    }

}