import { injectable } from "../domain/core/diContainer";
import { IDomHelper } from "../domain/infrastructure/IDomHelper";

@injectable()
export class DomHelperImpl implements IDomHelper{
    public subscribeInteraction(cb: () => void): void {
        document.body.addEventListener("mousemove", cb);
        document.body.addEventListener("mouseup", cb);
    }
    public unSubscribeInteraction(cb: () => void): void {
        document.body.removeEventListener("mousemove", cb);
        document.body.removeEventListener("mouseup", cb);
    }
    
}