import { injectable, inject } from "../../domain/core/diContainer";
import { IQCloudApi } from "../../domain/infrastructure/IQCloudApi";
import { IGetApplicationDetails, IGetApplicationDetailsParam } from "../../domain/useCase/IGetApplicationDetails";
import { IViewModelManager } from "../../domain/viewModel/IViewModelManager";

@injectable()
export class GetApplicationDetailsImpl implements IGetApplicationDetails {
    constructor(
        @inject(IQCloudApi) private readonly qcloudApi: IQCloudApi,
        @inject(IViewModelManager) private readonly viewModelManager: IViewModelManager,
    ) { }
    public async execute(param: IGetApplicationDetailsParam) {
        const details = await this.qcloudApi.getApplicationDetails(param.appID);
        this.viewModelManager.setApplicationDetails(param.appID, details);
    }
}