import { ExportType, IApplication, IApplicationDeployParameters, IFolder, IModel, IModule, IExportViewItem, IUser, ModelAdditionals, ModelType, IExportItem, IExportJobItem, IJobData, ModelHistoryType, IModelHistoryInfo, IExportJobStepItem, ITreeviewItem, IExportJobArtifactInfoItem, IModelBodyObject } from "../model/models";

export interface IGetModelResponse {
    ID: IModel["ID"];
    name: IModel["name"];
    checkouts?: Array<{ user: string, date: string }>;
    modelType: IModel["modelType"];
    modelAdditionals: IModel["additionals"];
    model?: Array<IModelBodyObject>;
}

export interface IGetModelHistoryResponse extends IGetModelResponse {
    modelID: string;
    historyType: string;
    ownerItem: string;
}

export interface IQCloudApi {
    createModel(params: { name: string, parentObject: IApplication | IModule | IFolder, modelBody: Array<IModelBodyObject> | undefined, modelType: ModelType, modelAdditionals?: ModelAdditionals }): PromiseLike<IModel>;
    createModule(name: string, ownerProjects?: Array<string>): PromiseLike<IModule>;
    createApplication(params: { name: string, description?: string, logo?: string, color?: string, deployParameters?: IApplicationDeployParameters }): PromiseLike<IApplication>;
    getModelInfo(ID: string, params?: { checkoutControl?: boolean; createCheckout?: boolean; }): Promise<IGetModelResponse>;
    getModel(ID: string, params?: { checkoutControl?: boolean, createCheckout?: boolean, retrieveBody?: boolean }): Promise<IGetModelResponse>;
    getModelBody(ID: string): Promise<{ ID: string, model: IGetModelResponse["model"] }>;
    updateModel(ID: string, params: {
        name?: string, model?: Array<IModelBodyObject>, checkin?: boolean, release?: boolean, publish?: boolean, shortComment?: string, comment?: string,
        parentObject?: IApplication | IModule | IFolder, modelType?: ModelType, modelAdditionals?: ModelAdditionals, ownerUser?: IUser
    }): PromiseLike<void>;
    updateModule(ID: string, params: { name?: string, ownerUser?: IUser, }): PromiseLike<void>;
    updateApplication(ID: string, params: { name?: string, description?: string, logo?: string, color?: string, ownerUser?: IUser, deployParameters?: IApplicationDeployParameters }): PromiseLike<void>;
    deleteModel(ID: string, addHistory?: boolean): PromiseLike<void>;
    deleteModule(ID: string, addHistory?: boolean): PromiseLike<void>;
    deleteFolder(ID: string, addHistory?: boolean): PromiseLike<void>;
    deleteApplication(ID: string, addHistory?: boolean): PromiseLike<void>;
    getUserDomains(customerEmail: string): PromiseLike<IUser["domains"]>;
    login(username: string, password: string, tenantID: string): PromiseLike<void>;
    listApplications(getDetails?: boolean): PromiseLike<Array<IApplication>>;
    getApplicationDetails(appID: string): PromiseLike<IApplication["details"]>;
    getApplication(appID: string): Promise<IApplication>;
    logout(): Promise<void>;
    getUserInfobyToken(): Promise<IUser>;
    getExportTypes(): Promise<Array<{ name: ExportType; text: string; }>>;
    getExportSelectionData(applicationID: string, exportType: ExportType): Promise<Array<IExportViewItem>>;
    listModelHistories(modelID: string, historyType?: ModelHistoryType | undefined): Promise<IModelHistoryInfo[]>;
    getModelHistory(historyID: string): Promise<IGetModelHistoryResponse>;
    createExportJob(app: IJobData["app"], exportType: ExportType, exportItems: Array<IExportItem>): Promise<IExportJobItem>;
    listExportJobs(): Promise<Array<IExportJobItem>>;
    listExportJobSteps(jobID: string): Promise<Array<IExportJobStepItem>>;
    listExportJobStepLogs(stepID: string): Promise<Array<string>>;
    listExportJobArtifacts(jobID: string): Promise<Array<IExportJobArtifactInfoItem>>;
    getExportJobArtifact(artifactID: string): Promise<string>;
    listTreeviewItems(applicationID: string): Promise<Array<ITreeviewItem>>;
}

export const IQCloudApi = Symbol.for("IQCloudApi");