import { container, inject, injectable } from "../../domain/core/diContainer";
import { IQCloudApi } from "../../domain/infrastructure/IQCloudApi";
import { createError } from "../../domain/model/shellError";
import { INotification } from "../../domain/presentation/INotification";
import { ITreeView, ITreeViewInstances } from "../../domain/presentation/ITreeView";
import { IObjectUseCase } from "../../domain/useCase/IObjectUseCase";
import { IStudio } from "../../domain/useCase/IStudio";
import { IViewModelManager } from "../../domain/viewModel/IViewModelManager";

@injectable()
export class ObjectUseCaseImpl implements IObjectUseCase {
    constructor(
        @inject(INotification) private readonly notification: INotification,
        @inject(IViewModelManager) private readonly viewModelManager: IViewModelManager,
        @inject(IQCloudApi) private readonly qcloudApi: IQCloudApi,
        @inject(IStudio) private readonly studio: IStudio,
    ) { }

    public async deleteObject(objectID: string) {
        const object = this.viewModelManager.findObject(objectID);

        if (!object){ throw createError({message: "object not found: " + objectID, type: "technical"}); }
        switch (object.objectType) {
            case "application": case "module": case "folder": case "model": break;
            default: throw createError({message: "can not delete " + object?.objectType + " with this use case ",type:"technical"});
        }

        const item = object;
        const confirm = await this.notification.showConfirm({
            title: "Are you sure?", text: `The ${item.objectType} '${item.name}' will be deleted permanently!`,
            type: "warning", approveText: "Delete", rejectText: "Cancel"
        });
        if (!confirm.approved) { return; }

        if (item.objectType == "model") {
            this.studio.closeItem(item, { silent: true });
        }
        const targetDict = {
            model: this.qcloudApi.deleteModel,
            module: this.qcloudApi.deleteModule,
            folder: this.qcloudApi.deleteFolder,
            application: this.qcloudApi.deleteApplication,
        };
        await targetDict[item.objectType].call(this.qcloudApi, item.ID, true);
        const feTreeview = container.resolve<ITreeView>(ITreeViewInstances.FileExplorer);
        feTreeview.removeItem(item.ID);
        this.viewModelManager.removeObject(item);
        this.notification.showNotification({ text: `'${item.name}' deleted successfully`, type: "success" });
    }

}