import { inject, injectable } from "../../domain/core/diContainer";
import { IQCloudApi } from "../../domain/infrastructure/IQCloudApi";
import { IListApplications, IListApplicationsParam } from "../../domain/useCase/IListApplications";
import { IViewModelManager } from "../../domain/viewModel/IViewModelManager";

@injectable()
export class ListApplicationsImpl implements IListApplications {
    constructor(
        @inject(IQCloudApi) private readonly qcloudApi: IQCloudApi,
        @inject(IViewModelManager) private readonly viewModelManager: IViewModelManager,
    ) { }
    public async execute(param: IListApplicationsParam) {
        const apps = await this.qcloudApi.listApplications(param.getDetails);
        this.viewModelManager.setApplicationList(apps);
        return apps;
    }
}