import { injectable } from "tsyringe";
import { IConfig } from "../domain/infrastructure/IConfig";
import * as environmentData from "../environment";
import { appSettingsDict } from "../appsetting";
import { createError } from "../domain/model/shellError";
import { ISettings } from "../domain/model/ISettings";

@injectable()
export class ConfigImpl implements IConfig {

    private settings: ISettings;
    constructor(
        private environment: string = "",
    ) {
        this.environment = environment || environmentData.startupEnvironment;
        this.settings = appSettingsDict[this.environment];
        if (!this.settings) { throw createError({ message: "unexpected environment: " + this.environment, type: "technical" }); }
    }

    public getValue<TKey extends keyof ISettings>(key: TKey) {
        return this.settings[key];
    }

    public getVersion(): string {
        return environmentData.version;
    }

    public getPresentationLayer(){
        return environmentData.presentationLayer;
    }
}
