import { injectable, inject } from "../../domain/core/diContainer";
import { IQCloudApi } from "../../domain/infrastructure/IQCloudApi";
import { IApplication } from "../../domain/model/models";
import { createError } from "../../domain/model/shellError";
import { INotification } from "../../domain/presentation/INotification";
import { IUpdateApplication, IUpdateApplicationParam } from "../../domain/useCase/IUpdateApplication";
import { IViewModelManager } from "../../domain/viewModel/IViewModelManager";

@injectable()
export class UpdateApplicationImpl implements IUpdateApplication {
    private oldApplication?: IApplication;

    constructor(
        @inject(IQCloudApi) private readonly qcloudApi: IQCloudApi,
        @inject(IViewModelManager) private readonly viewModelManager: IViewModelManager,
        @inject(INotification) private readonly notification: INotification,
    ) { }

    public async execute(param: IUpdateApplicationParam) {
        if (!param.ID) {
            throw createError({ message: `'ID' field is required!`, type: "business" });
        }

        this.oldApplication = this.viewModelManager.findObject(param.ID) as IApplication;

        const updatedApp = this.viewModelManager.objectUpdated({ ...this.oldApplication, ...param });
        updatedApp.synced = false;

        await this.qcloudApi.updateApplication(param.ID, param);

        this.notification.showNotification({ text: "Application updated successfully", type: "success" });
        updatedApp.synced = true;
    }

    public async fail(err: unknown) {
        if (!this.oldApplication) { return; }

        this.oldApplication = this.viewModelManager.objectUpdated(this.oldApplication);
        this.oldApplication.synced = true;
    }
}