export type Method =
    | 'get' | 'delete' | 'head' | 'options' | 'post' | 'put' | 'patch' | 'purge' | 'link' | 'unlink'
    | 'GET' | 'DELETE' | 'HEAD' | 'OPTIONS' | 'POST' | 'PUT' | 'PATCH' | 'PURGE' | 'LINK' | 'UNLINK';

export interface IRequest<TBody> {
    method: Method
    url: string;
    body?: TBody;
    headers?: Record<string, string | number | boolean>;
    timeout?: number;
    withCredentials?: boolean;
}
type DefinedRequest<TBody> = Omit<IRequest<TBody>, "method">;
export interface IPostRequest<TBody> extends DefinedRequest<TBody> { }
export interface IGetRequest extends Omit<DefinedRequest<undefined>, "body"> { }

export interface IResponse<TRespBody> {
    body: TRespBody;
    headers: Record<string, string> & { "set-cookie"?: Array<string> };
    status: number;
    statusText: string;
}

export interface INetwork {
    post<TRespBody, TBody>(request: IPostRequest<TBody>): Promise<IResponse<TRespBody>>;
    get<TRespBody>(request: IGetRequest): Promise<IResponse<TRespBody>>;
    request<TRespBody, TBody>(request: IRequest<TBody>): Promise<IResponse<TRespBody>>;
}
export const INetwork = Symbol.for("INetwork");