import { UnpromisedReturn } from "../../common/extendedTypes";
import { container, inject, injectable } from "../../domain/core/diContainer";
import { ILogger } from "../../domain/infrastructure/ILogger";
import { createHandledError, ShellError } from "../../domain/model/shellError";
import { ILoading } from "../../domain/presentation/ILoading";
import { INotification } from "../../domain/presentation/INotification";
import { IUseCase, IUseCaseFailReturn } from "../../domain/useCase/IUseCase";
import { IExecuteOptions, IUseCaseExecutor } from "../../domain/useCase/IUseCaseExecutor";

/**
 * <DOCS>
 * Use Case execution system (*A) is chosen instead of VM manuplation from presentation being watched by application (*B),
 * (*A) Use Case execution Ex: UI Code -> executor.executeUseCase<ICreateModule>(ICreateModule, module)
 * (*B) VM Manuplation Ex: UI Code -> viewModel.modules.push(module)
 * because; 
 * 1) Presentation would not be able to receive a return value from a data (VM) manuplation. (Result of create module (*B))
 * 2) backend manuplation of VM would cause infinite triggers (backend (CreateModule) -> client -> VM -(trigger watch)-> App(CM) -> backend(CreateModule) )
 */

@injectable()
export class UseCaseExecutorImpl implements IUseCaseExecutor {
    constructor(
        @inject(ILogger) private readonly logger: ILogger,
        @inject(INotification) private readonly notification: INotification,
        @inject(ILoading) private readonly loading: ILoading,
    ) { }

    public async executeUseCase<TUseCase extends IUseCase<UnpromisedReturn<TUseCase["execute"]>, any>>(useCaseInjectionToken: symbol, ...params: Parameters<TUseCase["execute"]>): Promise<UnpromisedReturn<TUseCase["execute"]>> {
        const useCase = container.resolve<TUseCase>(useCaseInjectionToken);
        try {
            const constructParam = params[0];
            const retVal = await useCase.execute(constructParam);
            return retVal;
        } catch (error: unknown) {
            let retVal: IUseCaseFailReturn | undefined;
            try {
                retVal = await useCase.fail?.(error) || undefined;
            } catch (error: unknown) {
                this.handleError(error, undefined);
            }

            this.handleError(error, retVal)
            throw createHandledError();
        }
    }
    public async resolveUseCase<TUseCase extends object>(useCaseInjectionToken: symbol) {
        const useCase = container.resolve<TUseCase>(useCaseInjectionToken);
        const proxyUseCase = new Proxy<TUseCase>(useCase, {
            get: (obj: any, prop) => {
                const retVal = obj[prop];
                if (retVal instanceof Function) {
                    return function () {
                        const args = Array.prototype.slice.call(arguments);
                        return (retVal as Function).apply(obj, args);
                    }
                }
                return retVal;
            },
        });
        return proxyUseCase;
    }

    public async execute<T>(cb: () => Promise<T>, options: IExecuteOptions) {
        try {
            if (options.loading) {
                this.loading.showLoading();
            }
            const retVal = await cb();
            return retVal;
        } catch (error) {
            this.handleError(error, undefined);
        } finally {
            if (options.loading) {
                this.loading.hideLoading();
            }
        }
    }
    public wrap(cb: () => Promise<void>, options: IExecuteOptions) {
        return async () => {
            return await this.execute(cb, options);
        }
    }

    private handleError(error: unknown, retVal: IUseCaseFailReturn | undefined) {
        if (error instanceof ShellError) {
            const title = error.shellError.title || error.shellError.type + " error";
            if (!retVal?.preventLog && !error.shellError.preventLog) {
                if (error.shellError.type == "business") {
                    this.logger.log({ level: "log", message: error.message });
                } else {
                    this.logger.log({ level: "error", message: error.message, error });
                }
            }
            if (!retVal?.preventNotification && !error.shellError.preventNotification) {
                this.notification.showNotification({
                    text: error.message, title, type: error.shellError.type == "business" ? "warning" : "error", copy: true
                });
            }
        } else {
            this.handleUnknownError(error, retVal);
        }
    }

    private handleUnknownError(error: unknown, retVal: IUseCaseFailReturn | undefined) {
        if (!retVal?.preventLog) {
            this.logger.log({ level: "error", message: "unhandled error", error });
        }
        if (!retVal?.preventNotification) {
            const text = (error as any).message;
            this.notification.showNotification({ text, title: "Error", type: "error", copy: true });
        }
    }
}
