import { injectable, inject } from "../../domain/core/diContainer";
import { IQCloudApi } from "../../domain/infrastructure/IQCloudApi";
import { IEditorManager } from "../../domain/objects/IEditorManager";
import { ISessionManager } from "../../domain/objects/ISesionManager";
import { ISidebarManager } from "../../domain/objects/ISidebarManager";
import { IRouter } from "../../domain/presentation/IRouter";
import { ILogout } from "../../domain/useCase/ILogout";
import { IViewModelManager } from "../../domain/viewModel/IViewModelManager";

@injectable()
export class LogoutImpl implements ILogout {
    constructor(
        @inject(IQCloudApi) private readonly qcloudApi: IQCloudApi,
        @inject(IViewModelManager) private readonly viewModelManager: IViewModelManager,
        @inject(ISessionManager) private readonly sessionManager: ISessionManager,
        @inject(IRouter) private readonly router: IRouter,
        @inject(ISidebarManager) private readonly sidebarManager: ISidebarManager,
        @inject(IEditorManager) private readonly editorManager: IEditorManager,
    ) { }
    public async execute() {
        await this.qcloudApi.logout();
        this.viewModelManager.reset();
        this.sessionManager.stop();
        this.sidebarManager.reset();
        this.editorManager.reset();
        this.router.push("/");
    }
}