import { injectable, inject } from "../../domain/core/diContainer";
import { IQCloudApi } from "../../domain/infrastructure/IQCloudApi";
import { IApplication } from "../../domain/model/models";
import { INotification } from "../../domain/presentation/INotification";
import { IDeleteApplication, IDeleteApplicationParam } from "../../domain/useCase/IDeleteApplication";
import { IViewModelManager } from "../../domain/viewModel/IViewModelManager";

@injectable()
export class DeleteApplicationImpl implements IDeleteApplication {
    constructor(
        @inject(IQCloudApi) private readonly qcloudApi: IQCloudApi,
        @inject(IViewModelManager) private readonly viewModelManager: IViewModelManager,
        @inject(INotification) private readonly notification: INotification,
    ) { }
    public async execute(param: IDeleteApplicationParam) {
        const confirm = await this.notification.showConfirm({
            title: "Are you sure?", text: `The app '${param.name}' will be deleted permanently!`,
            type: "warning", approveText: "Delete", rejectText: "Cancel"
        });
        if (!confirm.approved) { return; }

        await this.qcloudApi.deleteApplication(param.ID, true);
        const application = this.viewModelManager.findObject(param.ID) as IApplication;
        this.viewModelManager.removeApplication(application);
        this.notification.showNotification({ text: `Application '${application.name}' deleted successfully`, type: "success" });
    }
}