import { IStartUp } from "../domain/core/IStartUp";
import { IConfig } from "../domain/infrastructure/IConfig";
import { IDomHelper } from "../domain/infrastructure/IDomHelper";
import { IFrameMessanger } from "../domain/infrastructure/IFrameMessanger";
import { IHook } from "../domain/infrastructure/IHook";
import { IInlineEditor } from "../domain/infrastructure/IInlineEditor";
import { ILocalization } from "../domain/infrastructure/ILocalization";
import { ILogger } from "../domain/infrastructure/ILogger";
import { INetwork } from "../domain/infrastructure/INetwork";
import { IQCloudApi } from "../domain/infrastructure/IQCloudApi";
import { IThrottler } from "../domain/infrastructure/IThrotler";
import { ITimer } from "../domain/infrastructure/ITimer";
import { ConfigImpl } from "./configImpl";
import { DomHelperImpl } from "./domHelperImpl";
import { FrameMessanger } from "./frameMessanger";
import { HookImpl } from "./hookImpl";
import { InlineEditorImpl } from "./inlineEditorImpl";
import { LocalizationImpl } from "./localizationImpl";
import { LoggerImpl } from "./loggerImpl";
import { NetworkImpl } from "./networkImpl";
import { QCloudApiImpl } from "./qCloudApiImpl";
import { ThrottlerImpl } from "./throttlerImpl";
import { TimerImpl } from "./timerImpl";

export const infraStartUp: IStartUp = {
    register(container) {
        container.register<ILogger>(ILogger, LoggerImpl);
        container.register<IConfig>(IConfig, ConfigImpl);
        container.register<IQCloudApi>(IQCloudApi, QCloudApiImpl);
        container.register<IHook>(IHook, HookImpl);
        container.register<INetwork>(INetwork, NetworkImpl);
        container.register<IFrameMessanger>(IFrameMessanger, FrameMessanger);
        container.register<ITimer>(ITimer, TimerImpl);
        container.register<IDomHelper>(IDomHelper, DomHelperImpl);
        container.register<IThrottler>(IThrottler, ThrottlerImpl);
        container.register<IInlineEditor>(IInlineEditor, InlineEditorImpl);


        container.registerInstance<ILocalization>(ILocalization, new LocalizationImpl());
    }
}