import { IActionItem, IActionMenuItem, IActionSingleItem, ITreeItem, ITreeView, ITreeViewOptions } from "../../../domain/presentation/ITreeView";
import "./treeview.css";
//CONSIDER: could be moved from under vue3 to presentation directly (no vue dep.)
interface IActionItemSingleInternal extends IActionSingleItem {
    actionDiv: HTMLDivElement;
}
interface IActionItemMenuInternal extends IActionMenuItem {
    actionDiv: HTMLDivElement;
    actionUL: HTMLUListElement;
}
type IActionItemInternal = IActionItemSingleInternal | IActionItemMenuInternal;

interface ITreeItemInternal extends ITreeItem {
    parent?: ITreeItemInternal;
    children?: Array<ITreeItemInternal>;
    actions?: Array<IActionItemInternal>;
    actionHandlers?: {
        mouseover?: (event: MouseEvent) => void;
        mouseleave?: (event: MouseEvent) => void;
    };
    level: number;
    collapsed?: boolean;
    hidden?: boolean;
    rowContainer: HTMLDivElement;
    row: HTMLDivElement;
    indent: HTMLDivElement;
    twistie: HTMLDivElement;
    contentDiv: HTMLDivElement;
    contents: {
        text: HTMLDivElement;
        icon: HTMLDivElement;
        actions: HTMLDivElement;
    },
}

export class TreeView implements ITreeView {
    private root: HTMLDivElement = createDiv({ class: "root" });
    private headerItem?: ITreeItemInternal;
    private itemsRoot: HTMLDivElement = createDiv({ class: "items-root" });
    private itemDict: Record<string, ITreeItemInternal | undefined> = {};
    private itemDomDict = new Map<HTMLDivElement, ITreeItemInternal>();
    private itemList: Array<ITreeItemInternal> = [];
    private curSelected?: ITreeItemInternal;
    private contextMenuCloser?: () => void;
    private documentClickListener?: () => void;

    constructor() {}

    public setup(parent: HTMLElement, options: ITreeViewOptions = {}) {
        parent.appendChild(this.root);
        this.root.appendChild(this.itemsRoot);
        this.setHeader(options.header)
        this.itemsRoot.addEventListener("click", mouseEvent => {
            const targetContainer = findEventTargetWithClass<HTMLDivElement>(mouseEvent, "row-container");
            if (!targetContainer) { return; }
            const targetItem = this.itemDomDict.get(targetContainer);
            if (!targetItem) { return; }
            this.itemSelection(targetItem, true);
        });
        this.documentClickListener = () => {
            if (!this.contextMenuCloser) { return; }

            this.contextMenuCloser();
        };
        document.addEventListener("click", this.documentClickListener);
    }
    public setHeader(header: ITreeViewOptions["header"]) {
        if (header) {
            this.headerItem = this.arangeHeader(header);
            this.root.insertBefore(this.headerItem.rowContainer, this.itemsRoot)
        } else {
            if (!this.headerItem) { return; }
            this.root.removeChild(this.headerItem.rowContainer);
        }
    }
    public addItem(items: ITreeItem | Array<ITreeItem>, parentId?: string) {
        if (!(items instanceof Array)) { items = [items]; }
        items.forEach(item => {
            const newItem = this.convertTreeItemToInternal(item, false);
            this.importItem(newItem, parentId);
            if (item.children) {
                item.children.forEach(child => this.addItem(child, item.id));
            }
            setIcon(newItem);
        });
    }
    public getItem(itemId: string){
        return this.itemDict[itemId];
    }
    public updateItemName(itemId: string, itemName: string) { //TODO: bir item'in komple update olmas iin item satrn tekrar yarat. (not children)
        const item = this.getItem(itemId)!;
        item.name = itemName;
        item.contents.text.textContent = itemName;
    }
    public reset() {
        this.setHeader(undefined);
        this.itemList.forEach(item => this.removeItem(item.id));
        if (this.documentClickListener) {
            document.removeEventListener("click", this.documentClickListener);
        }
    }
    public removeItem(itemId: string) {
        const item = this.itemDict[itemId];
        if (!item) { return; }
        if (item.children) {
            item.children.forEach(child => this.removeItem(child.id));
        }
        if (item.parent) {
            item.parent.children = item.parent.children!.filter(child => child.id != itemId);
            setIcon(item.parent);
        }
        this.itemsRoot.removeChild(item.rowContainer);
        this.itemDomDict.delete(item.rowContainer);
        if (this.curSelected == item) { this.curSelected = undefined; }
        delete this.itemDict[itemId];
    }
    public addAction(itemId: string, actions: IActionItem | Array<IActionItem>) {
        const item = this.itemDict[itemId];
        if (!item) { return; }
        actions = actions instanceof Array ? actions : [actions];
        this.createActions(item, actions);
    }
    public removeAction(itemId: string, actionName: string) {
        const item = this.itemDict[itemId];
        if (!item || !item.actions) { return; }
        const actionIndex = item.actions?.findIndex(action => action.name == actionName);
        const action = item.actions[actionIndex];
        if (!action) { return; }
        action.actionDiv.parentElement?.removeChild(action.actionDiv);
        item.actions.splice(actionIndex, 1);
    }
    public collapseAll() {
        Object.keys(this.itemDict).forEach(itemId => this.toggleCollapse(this.itemDict[itemId]!, true));
    }
    public selectItem(itemId: string) {
        const targetItem = this.itemDict[itemId];
        if (!targetItem) { return; }

        this.itemSelection(targetItem, true);
    }
    public unselectItem(itemId: string) {
        const targetItem = this.itemDict[itemId];
        if (!targetItem) { return; }

        this.itemSelection(targetItem, false);
    }

    private importItem(newItem: ITreeItemInternal, parentId?: string) {
        const parent = parentId ? this.itemDict[parentId] : undefined;
        if (parent) {
            newItem.parent = parent;
            newItem.level = parent.level + 1;
        } else {
            newItem.level = 0;
        }
        newItem.twistie.style.paddingLeft = 6 * newItem.level + "px";
        createIndent(newItem);

        this.attachParent(newItem);
        if (parent) {
            setIcon(parent);
        }
        this.itemDict[newItem.id] = newItem;
        this.itemDomDict.set(newItem.rowContainer, newItem);
    }




    private attachParent(item: ITreeItemInternal) {
        if (item.parent && !item.parent.children) {
            item.parent.children = [];
        }
        const parentChildrenList = item.parent ? item.parent.children! : this.itemList;

        let targetIndex = parentChildrenList.findIndex(child => child.name > item.name);
        if (targetIndex == -1) {
            const parentLastChild: ITreeItemInternal | undefined = parentChildrenList[parentChildrenList.length - 1];
            if (!parentLastChild) {
                if (item.parent) {
                    this.itemsRoot.insertBefore(item.rowContainer, item.parent.rowContainer.nextElementSibling);
                } else {
                    this.itemsRoot.appendChild(item.rowContainer);
                }
            } else {
                const lastLeaf = findLastLeaf(parentLastChild);
                this.itemsRoot.insertBefore(item.rowContainer, lastLeaf.rowContainer.nextElementSibling);
            }
            parentChildrenList.push(item);
        } else {
            this.itemsRoot.insertBefore(item.rowContainer, parentChildrenList[targetIndex].rowContainer);
            parentChildrenList.splice(targetIndex, 0, item);
        }
    }



    private toggleCollapse(item: ITreeItemInternal, override?: boolean) {
        if (!item.children) { return; }

        item.collapsed = override === undefined ? !item.collapsed : override;
        setIcon(item);
        item.children?.forEach(child => this.toggleVisibility(child, override));
    }
    private toggleVisibility(item: ITreeItemInternal, override?: boolean) {
        item.hidden = override === undefined ? !item.hidden : override;
        item.rowContainer.style.display = item.hidden ? "none" : "block";
        if (item.collapsed) { return; }

        item.children?.forEach(child => this.toggleVisibility(child));
    }

    private itemSelection(item: ITreeItemInternal, selected: boolean) {
        const itemSelected = item.rowContainer.classList.contains("selected");

        if (selected) {
            this.toggleCollapse(item);
            if (itemSelected) { return; }

            item.rowContainer.classList.add("selected");
            item.cb?.select?.();
            this.curSelected && this.itemSelection(this.curSelected, false);
            this.curSelected = item;
        } else {
            if (!itemSelected) { return; }

            item.rowContainer.classList.remove("selected");
            this.curSelected = undefined;
        }
    }


    private convertTreeItemToInternal(item: ITreeItem, header: boolean): ITreeItemInternal {
        const retVal: ITreeItemInternal = {
            id: item.id,
            name: item.name,
            menu: item.menu,
            cb: item.cb,
            rowContainer: createDiv({ class: "row-container" }),
            row: createDiv({ style: { display: "flex", flexDirection: "row" }, class: "row" }),
            contentDiv: createDiv({ style: { display: "flex", flexDirection: "row", width: "100%" }, class: "content" }),
            contents: {
                text: createDiv({ text: item.name, style: { flex: "1", whiteSpace: "nowrap" }, class: header ? "header-text" : undefined }),
                icon: createDiv({ style: { minWidth: "10px" } }),
                actions: createDiv({ class: "actions" }),
            },
            indent: createDiv({ style: "", class: "indent" }),
            twistie: createDiv({ class: "twistie" }),
            level: 0,
            actionsWithRoot: item.actionsWithRoot,
        };

        retVal.rowContainer.appendChild(retVal.row);
        retVal.row.appendChild(retVal.indent);
        retVal.row.appendChild(retVal.twistie);
        retVal.row.appendChild(retVal.contentDiv);
        if (item.actions) {
            this.createActions(retVal, item.actions);
        }
        appendChildren(retVal.contentDiv, [retVal.contents.icon, retVal.contents.text, retVal.contents.actions]);
        return retVal;
    }
    private createActions(item: ITreeItemInternal, actions: Array<IActionItem>) {
        const actionsDiv = item.contents.actions;
        item.actions = item.actions || [];
        actions.forEach(action => {
            const actionDiv = createDiv({ text: action.name, class: "action" });
            let clickCB: () => void;
            if (action.type == "IActionMenuItem") {
                const actionUL = createElement("ul", { parent: document.body, class: "dropdown-menu", style: { display: "none" } });
                item.actions?.push({ type: "IActionMenuItem", name: action.name, children: action.children, actionDiv: actionDiv, actionUL });
                action.children.forEach(child => {
                    const li = createElement("li", { parent: actionUL, events: { click: () => { this.contextMenuCloser?.(); child.selectCb(); } } });
                    const a = createElement("a", { parent: li, class: "dropdown-item", text: child.name });
                    a.href = "#";
                });
                clickCB = () => {
                    this.contextMenuCloser?.();
                    this.contextMenuCloser = () => { actionUL.style.display = "none"; this.contextMenuCloser = undefined; };
                    actionUL.style.display = "block";
                    const divRect = actionDiv.getBoundingClientRect();
                    actionUL.style.top = divRect.top + divRect.height + "px";
                    actionUL.style.left = divRect.left + "px";
                };
            } else {
                item.actions?.push({ type: "IActionSingleItem", name: action.name, selectCb: action.selectCb, actionDiv: actionDiv });
                clickCB = () => action.selectCb?.();
            }
            actionDiv.addEventListener("click", mouseEvent => {
                mouseEvent.stopPropagation();
                clickCB();
            });
            actionsDiv.appendChild(actionDiv);
        });
        actionsDiv.style.display = "none";
        item.actionHandlers = {};
        item.actionHandlers.mouseover = () => actionsDiv.style.removeProperty("display");
        item.actionHandlers.mouseleave = () => actionsDiv.style.display = "none";
        item.rowContainer.addEventListener("mouseover", item.actionHandlers.mouseover);
        item.rowContainer.addEventListener("mouseleave", item.actionHandlers.mouseleave);
        if (item.actionsWithRoot) {
            this.itemsRoot.addEventListener("mouseover", item.actionHandlers.mouseover);
            this.itemsRoot.addEventListener("mouseleave", item.actionHandlers.mouseleave);
        }
    }

    private arangeHeader(headerOptions: NonNullable<ITreeViewOptions["header"]>) {
        const header = this.convertTreeItemToInternal({
            id: "",
            name: headerOptions.text,
            actions: headerOptions.actions,
            actionsWithRoot: headerOptions.actionsWithRoot,
        }, true);
        header.rowContainer.style.height = "23px";
        return header;
    }
}

function createIndent(newItem: ITreeItemInternal) {
    Array.from(new Array(newItem.level)).forEach(each => newItem.indent.appendChild(createDiv({ class: "indent-guide" })));
}

function setIcon(item: ITreeItemInternal) {
    item.contents.icon.textContent = getIconText(item);
}
function getIconText(item: ITreeItemInternal | ITreeItem) {
    const hasChildren = item.children?.length || 0 > 0;
    if (!hasChildren) { return ""; }

    return (item as ITreeItemInternal).collapsed ? "+" : "-";
}

function findLastLeaf(item: ITreeItemInternal): ITreeItemInternal {
    const children = item.children;
    if (children && children.length > 0) { return findLastLeaf(children[children.length - 1]); }

    return item;
}


function findEventTargetWithClass<T extends HTMLElement>(event: Event, searchClass: string) {
    const realSearch = "qcloud-tree-" + searchClass;
    let target = event.target as HTMLElement | null;
    while (target) {
        if (target.classList.contains(realSearch)) { return target as T; }
        target = target.parentElement;
    }
    return undefined;
}
function appendChildren(parent: HTMLElement, elements: Array<HTMLElement | undefined>) {
    elements.forEach(element => element && parent.appendChild(element));
}
function createDiv(options: { text?: string, style?: Partial<CSSStyleDeclaration>, class?: string | Array<string> } = {}): HTMLDivElement {
    const div = document.createElement("div");
    if (options.text) {
        div.textContent = options.text;
    }
    if (options.style) {
        Object.keys(options.style).forEach(style => div.style[style as any] = options.style![style as any]!);
    }
    if (options.class) {
        if (options.class instanceof Array) {
            options.class.forEach(eachClass => div.classList.add("qcloud-tree-" + eachClass));
        } else {
            div.classList.add("qcloud-tree-" + options.class);
        }
    }
    return div;
}
function createElement<TElement extends keyof HTMLElementTagNameMap>(tagName: TElement, options: { text?: string, style?: Partial<CSSStyleDeclaration>, class?: string | Array<string>, events?: { click?: () => void }, parent?: HTMLElement } = {}): HTMLElementTagNameMap[TElement] {
    const element = document.createElement(tagName);
    if (options.text) {
        element.textContent = options.text;
    }
    if (options.style) {
        Object.keys(options.style).forEach(style => element.style[style as any] = options.style![style as any]!);
    }
    if (options.events) {
        if (options.events.click) {
            element.addEventListener("click", options.events.click);
        }
    }
    if (options.class) {
        if (options.class instanceof Array) {
            options.class.forEach(eachClass => element.classList.add("qcloud-tree-" + eachClass));
        } else {
            element.classList.add("qcloud-tree-" + options.class);
        }
    }
    if (options.parent) {
        options.parent.appendChild(element);
    }
    return element;
}
