import { ISocketPackage, PModules } from "@stechquick/plateau-comm";
import { container, inject } from "../../../domain/core/diContainer";
import { IConfig } from "../../../domain/infrastructure/IConfig";
import { IFrameMessanger } from "../../../domain/infrastructure/IFrameMessanger";
import { IModelBodyObject, IModel } from "../../../domain/model/models";
import { IEntityDesignerEditor } from "../../../domain/objects/editors/IEntityDesignerEditor";

export class EntityDesignerEditorImpl implements IEntityDesignerEditor{
    public name = "entityDesigner" as "entityDesigner";
    private connected: boolean = false;

    constructor(
        //@inject(IViewModel) private readonly viewModel = container.resolve<IViewModel>(IViewModel),
        @inject(IFrameMessanger) private readonly frameMessanger = container.resolve<IFrameMessanger>(IFrameMessanger),
        @inject(IConfig) private readonly config = container.resolve<IConfig>(IConfig),
    ) {
        this.connect();
    }

    public connect(): void {
        if (this.connected) { return; }

        this.frameMessanger.connect(this.config.getValue("studio").editors.entity, message => {
            switch (message.msg.type) {
                case "IAmReady": this.iamReady({ id: message.id, msg: message.msg }); break;
                case "modifiedRequest": this.modifiedRequest(); break;
            }
        }, { init: true });
        this.connected = true;
    }
    public attach(element: HTMLElement): void {
        this.connect()
        this.frameMessanger.attach(element);
    }
    public detach(): void {
       this.frameMessanger.detach();
    }
    public show(show: boolean): void {
        this.frameMessanger.show(show);
    }
    public getModel(): Promise<{ model: IModelBodyObject[]; state: any; }> {
        throw new Error("Method not implemented.");
    }
    public async setModel(item: IModel) {
        this.frameMessanger.sendMessage("Quick", "setModel",
            {
                content: item.modelBody?.[0].model,
                fileName: item.name, type: "setModel", state: item.state,
            }, {}
        );
    }


    private iamReady(message: ISocketPackage<PModules["EntityDesigner"]["IAmReady"]>) {
        console.log("iam ready came from entity designer");
        this.frameMessanger.sendMessage("EntityDesigner", "IAmReadyResponse", {
            type: "IAmReadyResponse",
        }, {});
    }

    private modifiedRequest() {
    }

}