import { injectable, inject } from "../../domain/core/diContainer";
import { IQCloudApi } from "../../domain/infrastructure/IQCloudApi";
import { IModel } from "../../domain/model/models";
import { INotification } from "../../domain/presentation/INotification";
import { IDeleteModel, IDeleteModelParam } from "../../domain/useCase/IDeleteModel";
import { IViewModelManager } from "../../domain/viewModel/IViewModelManager";

@injectable()
export class DeleteModelImpl implements IDeleteModel {
    constructor(
        @inject(IQCloudApi) private readonly qcloudApi: IQCloudApi,
        @inject(IViewModelManager) private readonly viewModelManager: IViewModelManager,
        @inject(INotification) private readonly notification: INotification,
    ) { }
    public async execute(param: IDeleteModelParam) {
        const confirm = await this.notification.showConfirm({
            title: "Are you sure?", text: `'${param.name}' will be deleted permanently!`,
            type: "warning", approveText: "Delete", rejectText: "Cancel"
        });
        if (!confirm.approved) { return; }

        await this.qcloudApi.deleteModel(param.ID, true);
        const model = this.viewModelManager.findObject(param.ID) as IModel;
        this.viewModelManager.removeModel(model);
        this.notification.showNotification({ text: `'${model.name}' deleted successfully`, type: "success" });
    }
}