import { FrameCommunicator, ISocketPackage, MessageTypes, PModules } from "@stechquick/plateau-comm";

export type MessageHandler = (message: ISocketPackage<MessageTypes<PModules, "Quick"> | MessageTypes<PModules, "ProcessManager">>) => void;

export interface IFrameMessanger {
    connect(url: string, messageHandler: MessageHandler, options?: { init: boolean }): void;
    attach(element: HTMLElement): void;
    detach(): void;
    show(show: boolean): void;
    sendMessage<TModule extends keyof PModules, ReqType extends keyof PModules[TModule]>(module: TModule, messageType: ReqType, message: PModules[TModule][ReqType], options: Parameters<FrameCommunicator<PModules>["Send"]>["3"]): Promise<ISocketPackage<PModules[TModule][keyof PModules[TModule]]>> | undefined;
}
export const IFrameMessanger = Symbol.for("IFrameMessanger");