import { injectable, inject } from "../../domain/core/diContainer";
import { IQCloudApi } from "../../domain/infrastructure/IQCloudApi";
import { createError } from "../../domain/model/shellError";
import { IListExportJobStepLogs, IListExportJobStepLogsParam } from "../../domain/useCase/IListExportJobStepLogs";
import { IViewModelManager } from "../../domain/viewModel/IViewModelManager";

@injectable()
export class ListExportJobStepLogsImpl implements IListExportJobStepLogs {
    constructor(
        @inject(IQCloudApi) private readonly qcloudApi: IQCloudApi,
        @inject(IViewModelManager) private readonly viewModelManager: IViewModelManager,
    ) { }

    public async execute(param: IListExportJobStepLogsParam) {
        if (!param.stepID) {
            throw createError({ message: "'stepID' is required", type: "business" });
        }

        const logs = await this.qcloudApi.listExportJobStepLogs(param.stepID);
        this.viewModelManager.setExportJobStepLogs(param.stepID, logs);
    }
}