import { injectable } from "../domain/core/diContainer";
import { IThrottler } from "../domain/infrastructure/IThrotler";

@injectable()
export class ThrottlerImpl<T extends (...args: any) => any> implements IThrottler<T> {
    private cb?: T;
    private context?: any;
    private blocking = false;
    private waiting = false;
    private params?: Parameters<T>;
    private interval!: number;
    private timer?: NodeJS.Timeout;

    public setup(cb: T, interval: number, context?: any) {
        this.cb = cb;
        this.context = context;
        this.interval = interval;
    }

    public trigger(...params: Parameters<T>) {
        if (!this.cb) { throw new Error("must setup throttler before trigger"); }

        this.params = params;
        this.waiting = true;
        if (this.blocking) { return; }

        this.blocking = true;
        this.call();
    }

    public block() {
        this.timer && clearTimeout(this.timer);
        this.blocking = true;
        this.startTimer();
    }

    private startTimer() {
        if (!this.blocking) { return; }

        this.waiting = false;
        this.params = undefined;
        this.timer = setTimeout(() => this.call(), this.interval);
    }

    private call() {
        try {
            if (this.waiting) {
                this.cb!.call(this.context, ...this.params);
            } else {
                this.blocking = false;
            }
        } finally {
            this.startTimer();
        }
    }
}