export interface IShellError {
    message: string;
    title?: string;
    type: "business" | "technical";
    preventNotification?: boolean;
    preventLog?: boolean;
}

export class ShellError extends Error {
    public shellError: IShellError;

    constructor(error: IShellError) {
        super(error.message);
        this.shellError = error;
    }
}

export function createError(error: IShellError) {
    return new ShellError(error);
}
export function createHandledError() {
    return new ShellError({ message: "", type: "technical", preventLog: true, preventNotification: true });
}
export function notImplementedError(name: string) {
    return new ShellError({ type: "technical", title: "Not Implemented", message: `${name} not implemented.` });
}