import { inject, injectable } from "../../domain/core/diContainer";
import { IQCloudApi } from "../../domain/infrastructure/IQCloudApi";
import { ISessionManager } from "../../domain/objects/ISesionManager";
import { IRouter } from "../../domain/presentation/IRouter";
import { ILogin, ILoginParam } from "../../domain/useCase/ILogin";
import { IViewModelManager } from "../../domain/viewModel/IViewModelManager";

@injectable()
export class LoginImpl implements ILogin {
    constructor(
        @inject(IQCloudApi) private readonly qcloudApi: IQCloudApi,
        @inject(IViewModelManager) private readonly viewModelManager: IViewModelManager,
        @inject(ISessionManager) private readonly sessionManager: ISessionManager,
        @inject(IRouter) private readonly router: IRouter,
    ) { }
    public async execute(param: ILoginParam) {
        await this.qcloudApi.login(param.username, param.password, param.tenantID);
        this.viewModelManager.userLoggedIn = true;
        this.sessionManager.start();
        if (param.redirectToMain){
            this.router.push("/main/applicationList");
        }
    }
    // public async fail(err: unknown) {
    //     console.log("--------err", err);
    //     this.viewModelManager.showNotification({ text: (err as any).message, type: "error" });
    //     return { preventNotification: false, preventLog: false };
    // }

}