import { injectable, inject } from "../../domain/core/diContainer";
import { IQCloudApi } from "../../domain/infrastructure/IQCloudApi";
import { createError } from "../../domain/model/shellError";
import { IGetExportSelectionData, IGetExportSelectionDataParam } from "../../domain/useCase/IGetExportSelectionData";
import { IViewModelManager } from "../../domain/viewModel/IViewModelManager";

@injectable()
export class GetExportSelectionDataImpl implements IGetExportSelectionData {
    constructor(
        @inject(IQCloudApi) private readonly qcloudApi: IQCloudApi,
        @inject(IViewModelManager) private readonly viewModelManager: IViewModelManager,
    ) { }
    
    public async execute(param: IGetExportSelectionDataParam) {
        if (!(param.applicationID && param.exportType)) {
            throw createError({ message: "'applicationID' && 'exportType' are required", type: "business" });
        }

        const data = await this.qcloudApi.getExportSelectionData(param.applicationID, param.exportType);
        return data;
    }
}