import { inject, injectable } from "../domain/core/diContainer";
import { ILogger } from "../domain/infrastructure/ILogger";
import { IGetRequest, INetwork, IPostRequest, IRequest } from "../domain/infrastructure/INetwork";
import axios from "axios";
import { IConfig } from "../domain/infrastructure/IConfig";

let networkCounter = 0;
@injectable()
export class NetworkImpl implements INetwork {
    constructor(
        @inject(ILogger) private readonly logger: ILogger,
        @inject(IConfig) private readonly config: IConfig,
    ) { }

    public async post<TRespBody, TBody>(request: IPostRequest<TBody>) {
        const req = request as IRequest<TBody>;
        req.method = "POST";
        return await this.request<TRespBody, TBody>(req);
    }
    public async get<TRespBody>(request: IGetRequest) {
        const req = request as IRequest<undefined>;
        req.method = "GET";
        return await this.request<TRespBody, undefined>(req);
    }
    public async request<TRespBody, TBody>(request: IRequest<TBody>) {
        let { url, timeout, headers, body, method, withCredentials } = request;
        timeout = timeout ?? this.config.getValue("networkTimeout");

        const counter = networkCounter++;
        this.logger.log({ level: "debug", message: `request [${counter}] --> ${url}` });
        const resp = await axios.request<TRespBody>({ url, data: body, headers, timeout, method, withCredentials });
        this.logger.log({ level: "debug", message: `response [${counter}] <-- ${url}` });
        const retVal = {
            body: resp.data,
            headers: resp.headers,
            status: resp.status,
            statusText: resp.statusText,
        };
        return retVal;
    }
}