import { injectable } from "../../../domain/core/diContainer";
import { createError } from "../../../domain/model/shellError";
import { IZindex, IZindexOptions } from "../../../domain/presentation/IZindex";

interface IZindexOptionsInternal extends IZindexOptions {
}

@injectable()
export class ZIndexImpl implements IZindex {
    private indexDict: Record<string | symbol, Array<number> | undefined> = {};
    private optionsDict: Record<string | symbol, IZindexOptionsInternal | undefined> = {};
    constructor() { }
    public register(key: string | symbol, options: IZindexOptions) {
        if (this.optionsDict[key]) { throw createError({ message: `ZIndex already registered for: ${key.toString()}`, type: "technical" }); }

        this.assertFromGaps(key, options);
        this.optionsDict[key] = options as IZindexOptionsInternal;
    }

    public getZIndex(key: string | symbol) {
        const options = this.getOptions(key);
        let curItems = this.retrieveCurItems(key);
        let zindex = options.from;
        if (!curItems.length) {
            curItems.push(zindex);
        } else {
            const last = curItems[curItems.length - 1];
            zindex = last > zindex ? last : zindex;
            zindex++;
            curItems.push(zindex);
        }
        return zindex;
    }
    public removeZIndex(key: string | symbol, zindex?: number) {
        let curItems = this.retrieveCurItems(key);
        if (!curItems.length) { return; }
        if (zindex) {
            const index = curItems.findIndex(index => index == zindex);
            curItems.splice(index, 1);
        } else {
            curItems.splice(curItems.length - 1, 1);
        }
    }

    private retrieveCurItems(key: string | symbol) {
        let curItems = this.indexDict[key];
        if (!curItems) {
            curItems = this.indexDict[key] = [];
        }
        return curItems;
    }

    private getOptions(key: string | symbol) {
        const options = this.optionsDict[key];
        if (!options) { throw createError({ message: `ZIndex not registered for: ${key.toString()}`, type: "technical" }) };
        return options;
    }

    private assertFromGaps(key: string | symbol, options: IZindexOptionsInternal){
        Reflect.ownKeys(this.optionsDict).forEach(optionsKey => {
            const curOptions = this.optionsDict[optionsKey]!;
            if (Math.abs(curOptions.from - options.from) < 30) {
                throw createError({ message: `ZIndex registration error for: ${key.toString()}. From value too close: ${options.from}. Gap between from(s) should be > 30. ${optionsKey.toString()} from: ${curOptions.from}`, type: "technical" });
            }
        });
    }
}