import { injectable, inject } from "../../domain/core/diContainer";
import { IQCloudApi } from "../../domain/infrastructure/IQCloudApi";
import { IGetExportTypes, IGetExportTypesParam } from "../../domain/useCase/IGetExportTypes";

@injectable()
export class GetExportTypesImpl implements IGetExportTypes {
    constructor(
        @inject(IQCloudApi) private readonly qcloudApi: IQCloudApi,
    ) { }
    public async execute(param: IGetExportTypesParam) {
        switch (param.objectType) {
            default:
            case "application": return await this.qcloudApi.getExportTypes();
        }
    }
}