import { injectable, inject } from "../../domain/core/diContainer";
import { IQCloudApi } from "../../domain/infrastructure/IQCloudApi";
import { IRestoreUser, IRestoreUserParam } from "../../domain/useCase/IRestoreUser";
import { IViewModelManager } from "../../domain/viewModel/IViewModelManager";

@injectable()
export class RestoreUserImpl implements IRestoreUser {
    constructor(
        @inject(IQCloudApi) private readonly qcloudApi: IQCloudApi,
        @inject(IViewModelManager) private readonly viewModelManager: IViewModelManager,
    ) { }
    public async execute(param: IRestoreUserParam) {
        const userInfo = await this.qcloudApi.getUserInfobyToken();
        this.viewModelManager.initSessionUser(userInfo.email, userInfo.domains);
        this.viewModelManager.userLoggedIn = userInfo.loggedIn;
    }
}