export const appStartUp: IStartUp = {
    async register(container) {
        container.register<IViewModelManager>(IViewModelManager, ViewModelManager);

        //useCases
        container.register<ICreateModule>(ICreateModule, CreateModuleImpl);
        container.register<ICreateModel>(ICreateModel, CreateModelImpl);
        container.register<IGetUserDomains>(IGetUserDomains, GetUserDomainsImpl);
        container.register<ILogin>(ILogin, LoginImpl);
        container.register<IListApplications>(IListApplications, ListApplicationsImpl);
        container.register<IGetApplicationDetails>(IGetApplicationDetails, GetApplicationDetailsImpl);
        container.register<ILogout>(ILogout, LogoutImpl);
        container.register<ITimedOut>(ITimedOut, TimedOutImpl);
        container.register<ICheckLogin>(ICheckLogin, CheckLoginImpl);
        container.register<IInitStudio>(IInitStudio, InitStudioImpl);
        container.register<IRestoreUser>(IRestoreUser, RestoreUserImpl);
        container.register<ICreateApplication>(ICreateApplication, CreateApplicationImpl);
        container.register<IDeleteApplication>(IDeleteApplication, DeleteApplicationImpl);
        container.register<IUpdateApplication>(IUpdateApplication, UpdateApplicationImpl);
        container.register<IDeleteModule>(IDeleteModule, DeleteModuleImpl);
        container.register<IDeleteModel>(IDeleteModel, DeleteModelImpl);
        container.register<IObjectUseCase>(IObjectUseCase, ObjectUseCaseImpl);
        container.register<IUpdateModule>(IUpdateModule, UpdateModuleImpl);
        container.register<IGetExportTypes>(IGetExportTypes, GetExportTypesImpl);
        container.register<IGetExportSelectionData>(IGetExportSelectionData, GetExportSelectionDataImpl);
        container.register<ICreateExportJob>(ICreateExportJob, CreateExportJobImpl);
        container.register<IListModelVersions>(IListModelVersions, ListModelVersionsImpl);
        container.register<IListExportJobs>(IListExportJobs, ListExportJobsImpl);
        container.register<IListExportJobSteps>(IListExportJobSteps, ListExportJobStepsImpl);
        container.register<IListExportJobStepLogs>(IListExportJobStepLogs, ListExportJobStepLogsImpl);
        container.register<IListExportJobArtifacts>(IListExportJobArtifacts, ListExportJobArtifactsImpl);
        container.register<IGetExportJobArtifact>(IGetExportJobArtifact, GetExportJobArtifactImpl);
        container.register<IFileExplorer>(IFileExplorer, { useToken: delay(() => FileExplorer) });
        container.register<IStudio>(IStudio, Studio);
        container.register<IListModelHistories>(IListModelHistories, ListModelHistoriesImpl);
        container.register<IAutoLogin>(IAutoLogin, AutoLogin);
        //!!!DO_NOT_DELETE:THIS_LINE_FOR_AUTO_GENERATION:ADD_USECASE!!!
        
        //UseCase Executor
        container.register<IUseCaseExecutor>(IUseCaseExecutor, UseCaseExecutorImpl);
        
        //Objects
        await delayTillResolve([IViewModelProxifier], () => {
            ViewModelManager.reset();
            
            const quickEditor = new QuickEditorImpl();
            const processEditor = new ProcessEditorImpl(); 
            const processDesignEditor = new ProcessDesignEditorImpl();
            const entityDesignerEditor = new EntityDesignerEditorImpl();
            container.registerInstance<IQuickEditor>(IQuickEditor, quickEditor);
            container.registerInstance<IProcessEditor>(IProcessEditor, processEditor);
            container.registerInstance<IProcessDesignEditor>(IProcessDesignEditor, processDesignEditor);
            container.registerInstance<IEntityDesignerEditor>(IEntityDesignerEditor, entityDesignerEditor);
            container.registerInstance<IEditor>(IEditor, quickEditor);
            container.registerInstance<IEditor>(IEditor, processEditor);
            container.registerInstance<IEditor>(IEditor, processDesignEditor);
            container.registerInstance<IEditor>(IEditor, entityDesignerEditor);
            container.registerInstance(IEditorManager, new EditorManager());
            container.registerInstance(ISidebarManager, new SidebarManager());
            container.registerInstance(ISessionManager, new SessionManager());
            container.registerInstance<IProcessEditorUtil>(IProcessEditorUtil, new ProcessEditorUtil());
        });

        await delayWithResolve<IConfig>(IConfig, config => {
            if (config.getValue("debugging")) {
                const vmm = container.resolve<IViewModelManager>(IViewModelManager);
                const managers = {
                    mm: container.resolve(IEditorManager),
                    sbm: container.resolve(ISidebarManager),
                    sm: container.resolve(ISessionManager),
                };
                (window as any)._dev_22_0_22_0_22 = { container, vmm, managers }; // 18.02.2022 -> 22.02.2022 -> 22 0 22 0 22 :)
            }
        });
    }
}