import { container, injectable, inject } from "../../domain/core/diContainer";
import { IDomHelper } from "../../domain/infrastructure/IDomHelper";
import { IQCloudApi } from "../../domain/infrastructure/IQCloudApi";
import { IThrottler } from "../../domain/infrastructure/IThrotler";
import { ITimer } from "../../domain/infrastructure/ITimer";
import { ISessionManager } from "../../domain/objects/ISesionManager";
import { INotification } from "../../domain/presentation/INotification";
import { ILogout } from "../../domain/useCase/ILogout";
import { ITimedOut } from "../../domain/useCase/ITimedOut";
import { IUseCaseExecutor } from "../../domain/useCase/IUseCaseExecutor";

const sessionTimeout = 3 * 60 * 1000; //3 * 60 * 1000;
const interactionSessionRenewThrottle = 2 * 60 * 1000; //2 * 60 * 1000
const countdownTimeout = 30 * 1000; //30 * 1000
@injectable()
export class SessionManager implements ISessionManager {
    private interraction = () => this.throttler.trigger();

    constructor(
        @inject(IQCloudApi) private readonly qcloudApi: IQCloudApi = container.resolve<IQCloudApi>(IQCloudApi),
        @inject(INotification) private readonly notification: INotification = container.resolve<INotification>(INotification),
        @inject(ITimer) private readonly sessionTimer: ITimer = container.resolve<ITimer>(ITimer),
        @inject(ITimer) private readonly countdownTimer: ITimer = container.resolve<ITimer>(ITimer),
        @inject(IUseCaseExecutor) private readonly executor: IUseCaseExecutor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor),
        @inject(IDomHelper) private readonly domHelper = container.resolve<IDomHelper>(IDomHelper),
        @inject(IThrottler) private readonly throttler = container.resolve<IThrottler>(IThrottler),
    ) {
        this.throttler.setup(this.OnResumeInteract, interactionSessionRenewThrottle, this);
    }

    public start() {
        this.sessionTimer.setUp({ time: sessionTimeout, callback: () => this.onSessionEnding() });
        this.domHelper.subscribeInteraction(this.interraction);
        this.throttler.block();
    }
    public stop(): void {
        this.sessionTimer.reset();
        this.countdownTimer.reset();
        this.domHelper.unSubscribeInteraction(this.interraction);
    }

    private async OnResumeInteract() {
        console.log("devam diyorrrr");
        this.stop();
        await this.executor.execute(async () => {
            await this.qcloudApi.getUserInfobyToken();
            this.start();
        }, { loading: false });
    }

    private OnLogoutInteract() {
        console.log("yokum diyorrrr");

        this.stop();
        this.executor.execute(async () => {
            await this.executor.executeUseCase<ILogout>(ILogout);
        }, { loading: true });
    }

    private OnCountdownEnd() {
        this.executor.execute(async () => {
            await this.executor.executeUseCase<ITimedOut>(ITimedOut);
        }, { loading: true });
    }

    private async onSessionEnding() {
        this.domHelper.unSubscribeInteraction(this.interraction);
        this.countdownTimer.setUp({ time: countdownTimeout, callback: () => this.OnCountdownEnd() });

        const result = await this.notification.showConfirm({
            title: "Your session is expiring...",
            text: "What would you like to do?",
            type: "warning",
            timeout: countdownTimeout,
            pauseOnHover: false,
            pauseOnFocusLoss: false,
            approveText: "Resume",
            rejectText: "Logout",
        });
        result.approved ? this.OnResumeInteract() : this.OnLogoutInteract();
    }

}