import { injectable, inject } from "../../domain/core/diContainer";
import { IQCloudApi } from "../../domain/infrastructure/IQCloudApi";
import { IExportItem, IExportJobItem, IExportViewItem } from "../../domain/model/models";
import { createError } from "../../domain/model/shellError";
import { INotification } from "../../domain/presentation/INotification";
import { ICreateExportJob, ICreateExportJobParam } from "../../domain/useCase/ICreateExportJob";
import { IViewModelManager } from "../../domain/viewModel/IViewModelManager";

@injectable()
export class CreateExportJobImpl implements ICreateExportJob {
    constructor(
        @inject(IQCloudApi) private readonly qcloudApi: IQCloudApi,
        @inject(IViewModelManager) private readonly viewModelManager: IViewModelManager,
        @inject(INotification) private readonly notification: INotification,
    ) { }


    public async execute(param: ICreateExportJobParam) {
        if (!(param.app && param.exportType && param.selectionData)) {
            throw createError({ message: "'app', 'exportType', and 'selectionData' information required!", type: "business" });
        }

     

        const job = await this.qcloudApi.createExportJob(param.app, param.exportType,  param.selectionData);
        this.viewModelManager.exportJobCreated(job);

        this.notification.showNotification({ text: `Export job created successfully. ID: ${job.jobID}`, type: "success" });
    }
}