import { injectable, inject } from "../../domain/core/diContainer";
import { notImplementedError } from "../../domain/model/shellError";
import { ICheckLogin, ICheckLoginParam } from "../../domain/useCase/ICheckLogin";
import { IViewModel } from "../../domain/viewModel/IViewModel";
import { IViewModelManager } from "../../domain/viewModel/IViewModelManager";

@injectable()
export class CheckLoginImpl implements ICheckLogin {
    constructor(
        @inject(IViewModelManager) private readonly viewModelManager: IViewModelManager,
    ) { }
    public async execute(param: ICheckLoginParam) {
        switch(param.via){
            case "client": return this.viewModelManager.userLoggedIn;
            case "server": throw notImplementedError("CheckLogin via server");
        }
    }
}