import { injectable, inject } from "../../domain/core/diContainer";
import { IQCloudApi } from "../../domain/infrastructure/IQCloudApi";
import { IModule } from "../../domain/model/models";
import { createError } from "../../domain/model/shellError";
import { INotification } from "../../domain/presentation/INotification";
import { ITreeView, ITreeViewInstances } from "../../domain/presentation/ITreeView";
import { IUpdateModule, IUpdateModuleParam } from "../../domain/useCase/IUpdateModule";
import { IViewModelManager } from "../../domain/viewModel/IViewModelManager";

@injectable()
export class UpdateModuleImpl implements IUpdateModule {
    private oldModule?: IModule;

    constructor(
        @inject(IQCloudApi) private readonly qcloudApi: IQCloudApi,
        @inject(IViewModelManager) private readonly viewModelManager: IViewModelManager,
        @inject(INotification) private readonly notification: INotification,
        @inject(ITreeViewInstances.FileExplorer) private readonly feTreeview: ITreeView
    ) { }

    public async execute(param: IUpdateModuleParam) {
        if (!param.ID) {
            throw createError({ message: `'ID' field is required!`, type: "business" });
        }

        this.oldModule = this.viewModelManager.findObject(param.ID) as IModule;

        const updatedModule: IModule = { ...this.oldModule, ...param };

        this.viewModelManager.objectUpdated(updatedModule);

        await this.qcloudApi.updateModule(param.ID, param);

        if (param.name) {
            this.feTreeview.updateItemName(param.ID, param.name);
        }

        this.notification.showNotification({ text: "Module updated successfully", type: "success" });
    }

    public async fail(err: unknown) {
        if (!this.oldModule) { return; }

        this.viewModelManager.objectUpdated(this.oldModule);
    }
}