import { inject, injectable } from "../../domain/core/diContainer";
import { IQCloudApi } from "../../domain/infrastructure/IQCloudApi";
import { IModule } from "../../domain/model/models";
import { createError } from "../../domain/model/shellError";
import { INotification } from "../../domain/presentation/INotification";
import { ICreateModule, ICreateModuleParam } from "../../domain/useCase/ICreateModule";
import { IFileExplorer } from "../../domain/useCase/IFileExplorer";
import { IViewModelManager } from "../../domain/viewModel/IViewModelManager";


@injectable()
export class CreateModuleImpl implements ICreateModule {
    private module?: IModule;
    
    constructor(
        @inject(IQCloudApi) private readonly qcloudApi: IQCloudApi,
        @inject(IViewModelManager) private readonly viewModelManager: IViewModelManager,
        @inject(INotification) private readonly notification: INotification,
        @inject(IFileExplorer) private readonly fileExplorer: IFileExplorer,
    ) { }

    async execute(param: ICreateModuleParam) {
        const name = param.name;

        this.module = { ID: "", synced: false, objectType: "module", name, children: [] };
        this.viewModelManager.moduleCreated(this.module);

        const createdModule = await this.qcloudApi.createModule(name, param.ownerProjects);

        this.module.ID = createdModule.ID;
        this.module.synced = true;
        this.fileExplorer.addNewItem(this.module);
        this.notification.showNotification({ text: `Module '${name}' created successfully`, type: "success" });
        return this.module;
    }
    async fail(err: unknown) {
        if (!this.module) { return; }

        this.viewModelManager.removeModule(this.module);
    }
}