import { injectable, inject } from "../../domain/core/diContainer";
import { IQCloudApi } from "../../domain/infrastructure/IQCloudApi";
import { IListModelHistories, IListModelHistoriesParam } from "../../domain/useCase/IListModelHistories";


@injectable()
export class ListModelHistoriesImpl implements IListModelHistories {
    constructor(
        @inject(IQCloudApi) private readonly qcloudApi: IQCloudApi,
    ) { }
    public async execute(param: IListModelHistoriesParam) {
        const histories = await this.qcloudApi.listModelHistories(param.modelId);
        return histories;
    }
}