import { IFrameMessanger, MessageHandler } from "../domain/infrastructure/IFrameMessanger";
import { PlateauMessaging as PM, PModules, FrameCommunicator } from "@stechquick/plateau-comm";

export class FrameMessanger implements IFrameMessanger {
    private frame: HTMLIFrameElement = document.createElement("iframe");
    private pm?: FrameCommunicator<PModules>;
    private url?: string;
    private messageHandler?: MessageHandler;
    private attachedElement?: HTMLElement;
    private resizeObserver?: ResizeObserver;
    constructor() { }

    public connect(url: string, messageHandler: MessageHandler, options: { init?: boolean } = {}): void {
        this.url = url;
        this.messageHandler = messageHandler;
        document.body.appendChild(this.frame);
        if (options.init) {
            this.initFrame();
        }
    }
    public attach(element: HTMLElement) {
        const fStyle = this.frame.style;
        fStyle.position = "absolute";
        fStyle.margin = "0px";
        fStyle.border = "0px";
        this.detach();
        this.attachedElement = element;
        this.resizeObserver = new ResizeObserver(entries => {
            if (fStyle.display == "none") { return; }
            this.resizeTo(element);
        });
        this.resizeObserver.observe(element);
        this.resizeTo(element);
    }
    public detach() {
        this.resizeObserver?.disconnect();
        this.show(false);
    }

    public show(show: boolean) {
        if (show) {
            this.initFrame();
            this.resizeTo(this.attachedElement!);
        }
        this.frame!.style.display = show ? "block" : "none";
    }

    public sendMessage<TModule extends keyof PModules, ReqType extends keyof PModules[TModule]>(module: TModule, messageType: ReqType, message: PModules[TModule][ReqType], options: Parameters<FrameCommunicator<PModules>["Send"]>["3"]) {
        return this.pm?.Send<TModule, ReqType>(module, messageType, message, options);
    }

    private initFrame() {
        if (this.frame.src) { return; }

        this.frame.src = this.url!;
        this.pm = PM.CreatePlateauMessaging(this.frame.contentWindow!);
        if (this.messageHandler) {
            this.pm.SubscribeAll(this.messageHandler);
        }
    }
    private resizeTo(element: HTMLElement) {
        const fStyle = this.frame.style;
        const rect = element.getBoundingClientRect();
        fStyle.left = rect.left + "px";
        fStyle.top = rect.top + "px";
        fStyle.width = rect.width + "px";
        fStyle.height = rect.height + "px";
    }
}